import { createContext, useState, useMemo, useCallback } from "react";

interface AlertContextType {
  /**
   * alerts: [loading, success, error]
   */
  alerts: Alert;
  /**
   * setAlerts: display one alert a time
   */
  setAlerts: (alert: Partial<Alert>) => void;
}

interface Alert {
  loading: boolean;
  success: null | string;
  error: null | string;
  actionButtons?: null | JSX.Element;
}

const defaults: Alert = {
  loading: false,
  success: null,
  error: null,
};

export const AlertContext = createContext<AlertContextType>({
  alerts: { ...defaults },
  setAlerts: () => {},
});

export const useAlertContextDefaults = (): [
  AlertContextType,
  Alert,
  (alert: Partial<Alert>) => void
] => {
  const [alerts, setAlertsState] = useState(defaults);

  const setAlerts = useCallback(
    (alert: Partial<Alert>) => {
      setAlertsState({ ...defaults, ...alert });
    },
    [setAlertsState]
  );

  const alertContextProviderDefaults = useMemo(
    () => ({
      alerts,
      setAlerts,
    }),
    [alerts, setAlerts]
  );
  return [alertContextProviderDefaults, alerts, setAlerts];
};
