import { useContext, useCallback, memo } from "react";

import Backdrop from "@mui/material/Backdrop";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";

import { KeyboardContext } from "../../contexts/keyboard";
import { styles } from "./global-keyboard.styles";

/**
 * A simple modal to render children updated in KeyboardContext
 */
export const GlobalKeyboardModal = memo(() => {
  const { keyboard, setKeyboard } = useContext(KeyboardContext);
  const { open, children } = keyboard;

  const handleClose = useCallback(() => {
    setKeyboard({ ...keyboard, open: false });
  }, [keyboard, setKeyboard]);

  return (
    <Modal
      aria-labelledby="global-keyboard-modal-title"
      aria-describedby="global-keyboard-modal-description"
      open={open}
      onClose={handleClose}
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
      }}
    >
      <Box sx={styles.modalContent}>{children}</Box>
    </Modal>
  );
});
GlobalKeyboardModal.displayName = "GlobalKeyboardModal";
