import {
  MY_APPS_HANDSHAKE_MSG,
  MAWP_HANDSHAKE_RESP_MSG,
  MY_APPS_WEB_URL,
  WNDW_MESSAGE_EVENTS,
  MY_APPS_DATA
} from './mawp-constants';
import StorageService from './services/storage.service';
import UtilityService from './services/utility.service';

export class MawpEvents {
  listen() {
    window.addEventListener(WNDW_MESSAGE_EVENTS, function (event) {
      if (!event.data || (!event.origin.includes('localhost') && !event.origin.includes('walmart.com') &&
        !event.origin.includes('wal-mart.com') && !event.origin.includes('walmart.net') &&
        !event.origin.includes('wal-mart.net'))) {
        return;
      }

      if (event.data === MY_APPS_HANDSHAKE_MSG) { // determine if handshake message from my-apps
        event.source.postMessage(MAWP_HANDSHAKE_RESP_MSG, event.origin); // respond with successful handshake
      } else {
        try {
          const data = UtilityService.parseJSON(event.data);

          if (data && data.id && data.id === MY_APPS_DATA) { // determine if data is from My Apps
            StorageService.setExternalData(data);
          }
        } catch (e) {
          /* do nothing */
        }
      }
    }, false);
  }

  handshakeEvent() {
    parent.postMessage('[my-apps-web-plugin] - HANDSHAKE',
      MY_APPS_WEB_URL);
  }

  goBackToMyAppList() {
    parent.postMessage('[my-apps-web-plugin] - GO BACK_TO_APP_LIST', MY_APPS_WEB_URL);
  }

  goBackToLogin() {
    parent.postMessage('[my-apps-web-plugin] - GO BACK_TO_LOGIN', MY_APPS_WEB_URL);
  }

  /**
   * method to post data to myapps web window
   * @param targetUrl target window url
   * @param data data to be posted to myapps web
   * @param errorCallback error callback method
   * @param errorMessage error message to be displayed if error
   */
  postDataToMyAppsWeb(targetUrl, data, errorCallback, errorMessage) {
    try {
      let windowOpener = UtilityService.getWindowOpener();

      if (windowOpener && targetUrl) {
        windowOpener.postMessage(data, targetUrl);
      } else {
        errorCallback(errorMessage);
      }
    } catch (e) {
      errorCallback(e);
    }
  }
}
