import { useContext, memo } from "react";

import { AlertContext } from "../../contexts/alert";
import { ApiStatus } from "./api-status";

/**
 * Listen to the data from AlertContext
 * and pass the values to child component
 */
export const GlobalApiStatus = memo(() => {
  const { alerts } = useContext(AlertContext);
  const { success, error, loading, actionButtons } = alerts;
  return (
    <ApiStatus
      success={success}
      error={error}
      loading={loading}
      actionButtons={actionButtons}
    />
  );
});
GlobalApiStatus.displayName = "GlobalApiStatus";
