import UtilityService from './utility.service';

const MY_APPS_KEY_URL = 'https://prod-myapps-launcher.prod.cloud.wal-mart.com/my-apps/key?pepper=';

/**
 * method to add header values to HTTP object
 * @param http XMLHTTP Object
 * @param headerData header object data
 */
function addHeader(http, headerData) {
  for (let key in headerData) {
    http.setRequestHeader(key, headerData[key]);
  }
}

/**
 * @whatIdDoes this class serves as an HTTP service for my apps backend
 * XmlHTTPRequest help https://stackoverflow.com/questions/247483/http-get-request-in-javascript
 */
export default class HttpService {

  /**
   * My Apps Backend GET /key endpoint
   * @param appId
   * @param callback
   */
  static getKey(appId, callback) {
    const HTTP = new XMLHttpRequest();

    HTTP.open('GET', MY_APPS_KEY_URL + appId, true); // true for asynchronous
    HTTP.onload = () => {
      if (HTTP.readyState === 4) {
        if (HTTP.status === 200) {
          callback(UtilityService.parseJSON(HTTP.responseText));
        } else {
          callback(UtilityService.errorMessage(HTTP));
        }
      }
    };
    HTTP.onerror = function () {
      callback(UtilityService.errorMessage(HTTP));
    };
    HTTP.send(null);
  }

  /**
   * method to do async xhr GET request
   * @param url url to get data from
   * @param setHeader callback method to set header
   * @param headerData header data to be set in callback method
   */
  static get(url, setHeader, headerData) {
    return new Promise((success, error) => {
      const HTTP = new XMLHttpRequest();

      HTTP.open('GET', url, true); // true for asynchronous
      if (setHeader) {
        setHeader(HTTP, headerData);
      }
      HTTP.onload = () => {
        if (HTTP.readyState === 4) {
          if (HTTP.status === 200) {
            success(UtilityService.parseJSON(HTTP.responseText));
          } else {
            error(UtilityService.errorMessage(HTTP));
          }
        }
      };
      HTTP.onerror = function () {
        error(UtilityService.errorMessage(HTTP));
      };
      HTTP.send(null);
    });
  }

  /**
   * method to do async xhr PUT request
   * @param url url to put data to
   * @param setHeader callback method to set header
   * @param headerData header data to be set in callback method}
   * @param body request body data
   */
  static put(url, setHeader, headerData, body) {
    return new Promise((success, error) => {
      const HTTP = new XMLHttpRequest();

      HTTP.open('PUT', url, true); // true for asynchronous
      if (setHeader) {
        setHeader(HTTP, headerData);
      }
      HTTP.onload = () => {
        if (HTTP.readyState === 4) {
          if (HTTP.status === 200) {
            success(UtilityService.parseJSON(HTTP.responseText));
          } else {
            error(UtilityService.errorMessage(HTTP));
          }
        }
      };
      HTTP.onerror = function () {
        error(UtilityService.errorMessage(HTTP));
      };
      HTTP.send(body || null);
    });
  }

  /**
   * common method to handle http requests
   * @param url request url
   * @param header header object
   * @param method HTTP method
   * @param body request body
   */
  static send(url, header, method, body = null) {
    return new Promise((success, error) => {
      const HTTP = new XMLHttpRequest();

      HTTP.open(method, url, true); // true for asynchronous
      addHeader(HTTP, header);
      HTTP.onload = () => {
        if (HTTP.readyState === 4) {
          if (HTTP.status === 200) {
            success(UtilityService.parseJSON(HTTP.responseText));
          } else {
            error(UtilityService.errorMessage(HTTP));
          }
        }
      };
      HTTP.onerror = function () {
        error(UtilityService.errorMessage(HTTP));
      };
      HTTP.send(body ? JSON.stringify(body) : body);
    });
  }
}
