import StorageService, { COOKIE_KEY } from './storage.service';

export default class UtilityService {

  static isValidCookie() {
    return StorageService.getCookie(COOKIE_KEY).length > 0;
  }

  /**
   * Default error handler. Always return this object.
   * @param HTTP - XMLHttpRequest
   * @return {{error: string, status: *}}
   */
  static errorMessage(HTTP) {
    let error = HTTP.statusText;

    return {
      error: error === '' ? 'The My Apps Web Plugin was unable to get response from the service.' : error,
      status: HTTP.status,
      responseURL: HTTP.responseURL
    };
  }

  /**
   * Parse JSON to an object
   * @param data
   */
  static parseJSON(data) {
    let parsedData = {};

    try {
      parsedData = JSON.parse(data);
    } catch (e) {
      /* ignore invalid data */
    }
    return parsedData;
  }

  static getWindowOpener() {
    return window.opener;
  }

  static setWindowOpener(data) {
    window.opener = data;
  }

  static getToken(data) {
    return data ? data.token || '' : '';
  }

  static getBusinessUnitId(data) {
    return data ? data.businessUnitId || '' : '';
  }

  static getUserId(data) {
    return data ? data.userId || '' : '';
  }

  static getSecurityId(data) {
    return data ? data.securityId || '' : '';
  }

  static getWindowOpener() {
    return window.opener;
  }
}
