import axios, { AxiosRequestConfig } from "axios";
import { v4 as uuid } from "uuid";

import { generateBaseUrls } from "../constants/baseUrls";
import { DeliveryDocument, ScannedDataListitem } from "../types/instruction";
import { PickToTransfer } from "../types/item-transfer";
import { MyAppsData } from "../types/my-apps";
import { ReprintRequestBody } from "../types/printing";
import { Log } from "./logger/logger";

// App level constants
export const DELIVERY_STATUS = "";
export const ENTERED_QTY_UOM = "EA";

const ENDGAME_PATH = "/endgame";

const {
  receivingBaseUrl: RECEIVING_BASE_URL,
  gdmBaseUrl: GDM_BASE_URL,
  ofBaseUrl: OF_BASE_URL,
  loggerUrl: LOGGER_URL,
  inventoryBaseUrl: INVENTORY_BASE_URL,
} = generateBaseUrls();

export interface Init {
  /**
   * receivingBaseUrl: Receiving team's base url
   */
  receivingBaseUrl: string;
  /**
   * gdmBaseUrl: GDM team's base url
   */
  gdmBaseUrl: string;
  /**
   * ofBaseUrl: OF team's base url
   */
  ofBaseUrl: string;
  /**
   * loggerUrl: url for posting logs
   */
  loggerUrl: string;
  /**
   * inventoryBaseUrl: inventory team's base url
   */
  inventoryBaseUrl: string;
  /**
   * timeout: timeout for apis in millisecs
   */
  timeout: string;
  /**
   * decantTimeout: timeout for decant flow in millisecs
   */
  decantTimeout: string;
}

export interface GetScannedUpcInfoRequest {
  deliveryNumber: string;
  upcNumber: string;
}

export interface GetBreakpackInstructionRequest {
  trackingId: string;
  cartId: string;
}

export interface GetQtyCorrectionInfoRequest {
  trackingId: string;
  adjustQuantity: number;
  adjustQuantityUOM: string;
  inventoryQuantity: number;
}

export interface PostTagForContainer {
  containerId: string;
  tags: ContainerTag[];
  facilityNumber: string;
  facilityCode: string;
}

export interface ContainerTag {
  tag : string,
	action: string
}

export interface GetReceiveInstructionRequest {
  deliveryNumber: string;
  upcNumber: string;
  doorNumber: string;
  deliveryDocuments: DeliveryDocument[] | null;
  enteredQty: number | null;
  isMultiSKUItem: boolean;
  isVendorComplianceValidated?: boolean;
  gs1Barcode?: string | null;
  previouslyScannedDataList?: string[] | null;
  scannedDataList?: ScannedDataListitem[] | null;
  receivingType?: string | null;
  additionalParams?: {} | null;
}

export interface getContainerDetailsRequest {
  location: String,
  status: number,
  correlationId: string | number | boolean
}

class Api {
  timeout = 0;
  messageId = uuid();
  receivingBaseUrl = "";
  gdmBaseUrl = "";
  ofBaseUrl = "";
  inventoryBaseUrl = "";
  loggerUrl = "";
  decantTimeout = 0;
  enableSlotSuggestion = false;

  init(myAppsData: MyAppsData) {
    const {
      receivingBaseUrl,
      gdmBaseUrl,
      ofBaseUrl,
      apiTimeout,
      loggerUrl,
      inventoryBaseUrl,
      decantTimeout,
      enableSlotSuggestion,
    } = myAppsData.dynamicConfig;

    this.receivingBaseUrl = receivingBaseUrl || RECEIVING_BASE_URL;
    this.gdmBaseUrl = gdmBaseUrl || GDM_BASE_URL;
    this.ofBaseUrl = ofBaseUrl || OF_BASE_URL;
    this.inventoryBaseUrl = inventoryBaseUrl || INVENTORY_BASE_URL;
    this.loggerUrl = loggerUrl || LOGGER_URL;
    this.timeout = parseInt(apiTimeout?.toString() || "0");
    this.decantTimeout = parseInt(decantTimeout?.toString() || "0");
    this.setGlobalHeaders(myAppsData);
    this.enableSlotSuggestion = enableSlotSuggestion || false;
  }

  setGlobalHeaders(myAppsData: MyAppsData) {
    axios.defaults.headers.common["facilityCountryCode"] =
      myAppsData.countryCode;
    axios.defaults.headers.common["facilityNum"] = myAppsData.businessUnitId;
    axios.defaults.headers.common["WMT-UserId"] = myAppsData.userId;
    axios.defaults.headers.common["Content-Type"] = "application/json";
  }

  generateMessageId() {
    this.messageId = uuid();
  }

  getTclInfo(label: string): AxiosRequestConfig {
    return {
      method: "GET",
      url: `${this.receivingBaseUrl}${ENDGAME_PATH}/tcls/${label}`,
      headers: {
        "WMT-CorrelationId": uuid(),
      },
      timeout: this.timeout,
    };
  }

  getLpnInfo(
    trackingId: string,
    isReEngageDecantFlow = false
  ): AxiosRequestConfig {
    return {
      method: "GET",
      url: `${this.receivingBaseUrl}/containers/${trackingId}?includeChilds=true&includeInventoryData=true&uom=${ENTERED_QTY_UOM}&isReEngageDecantFlow=${isReEngageDecantFlow}`,
      headers: {
        "WMT-CorrelationId": uuid(),
      },
      timeout: this.timeout,
    };
  }

  getDockTagInfo(label: string): AxiosRequestConfig {
    return {
      method: "PUT",
      url: `${this.receivingBaseUrl}/docktags/${label}/receive`,
      headers: {
        "WMT-CorrelationId": uuid(),
      },
      timeout: this.timeout,
    };
  }

  getLPNorDockTag(lpnOrDockTag: string, doorNumber: string | undefined): AxiosRequestConfig {
    return {
      method: "PUT",
      url: `${this.receivingBaseUrl}/lpn/${lpnOrDockTag}/receive?doorNumber=${doorNumber}`,
      headers: {
        "WMT-CorrelationId": uuid(),
      },
      timeout: this.timeout,
    };
  }

  getContainerInfo(label: string): AxiosRequestConfig {
    return {
      method: "GET",
      url: `${this.inventoryBaseUrl}/inventory/inventories/containers/${label}`,
      headers: {
        "WMT-CorrelationId": uuid(),
        requestOriginator: "receiving",
      },
      timeout: this.timeout,
    };
  }

  getScannedUpcInfo(request: GetScannedUpcInfoRequest): AxiosRequestConfig {
    return {
      method: "GET",
      url: `${this.gdmBaseUrl}/document/v2/deliveries/${request.deliveryNumber}/deliveryDocuments/itemupcs/${request.upcNumber}`,
      headers: {
        "WMT-CorrelationId": uuid(),
      },
      timeout: this.timeout,
    };
  }

  getReceiveInstruction(
    request: GetReceiveInstructionRequest
  ): AxiosRequestConfig {
    return {
      method: "POST",
      url: `${this.receivingBaseUrl}/instructions/request`,
      headers: {
        "WMT-CorrelationId": uuid(),
      },
      timeout: this.timeout,
      data: {
        messageId: uuid(),
        deliveryStatus: DELIVERY_STATUS,
        enteredQtyUOM: ENTERED_QTY_UOM,
        ...request,
      },
    };
  }

  postQtyCorrection(request: GetQtyCorrectionInfoRequest): AxiosRequestConfig {
    return {
      method: "POST",
      url: `${this.receivingBaseUrl}/containers/${request.trackingId}/adjust`,
      headers: {
        "WMT-CorrelationId": uuid(),
      },
      timeout: this.timeout,
      data: {
        adjustQuantity: request.adjustQuantity,
        adjustQuantityUOM: request.adjustQuantityUOM,
        inventoryQuantity: request.inventoryQuantity,
        printerId: null,
        isInventoryReceivingCorrection: false,
      },
    };
  }

  getBreakpackInstruction(
    request: GetBreakpackInstructionRequest
  ): AxiosRequestConfig {
    const endUrl = this.enableSlotSuggestion
      ? `?workstation=${request.cartId}`
      : "";
    return {
      method: "GET",
      url: `${this.ofBaseUrl}/put/instruction/${request.trackingId}${endUrl}`,
      headers: {
        "WMT-CorrelationId": uuid(),
      },
      timeout: this.timeout,
    };
  }

  postItemTransfer(data: any): AxiosRequestConfig {
    console.log("value of reEngageDecant = ", data.isReEngageDecantFlow);

    return {
      method: "POST",
      url: `${this.ofBaseUrl}/put/itemTransfer`,
      params: {
        isReEngageDecantFlow: data.isReEngageDecantFlow,
      },
      headers: {
        "WMT-CorrelationId": uuid(),
      },
      timeout: this.timeout,
      data: data.transferPicksRequest,
    };
  }

  postReprintLabelFromAtlas(reprint: ReprintRequestBody): AxiosRequestConfig {
    return {
      method: "POST",
      url: `${this.receivingBaseUrl}/containers/labels/reprint`,
      headers: {
        "WMT-CorrelationId": uuid(),
      },
      timeout: this.timeout,
      data: reprint,
    };
  }

  putCloseTote(slotId: string): AxiosRequestConfig {
    return {
      method: "PUT",
      url: `${this.ofBaseUrl}/put/closeContainer/${slotId}`,
      headers: {
        "WMT-CorrelationId": uuid(),
      },
      timeout: this.timeout,
    };
  }

  postLoggerLogs(log: Log, splunkToken = ""): AxiosRequestConfig {
    return {
      method: "POST",
      url: this.loggerUrl,
      timeout: this.timeout,
      headers: {
        Authorization: splunkToken,
        "Content-Type": "text/plain",
      },
      data: {
        sourcetype: "JSON",
        index: "receiving-web",
        event: log,
      },
    };
  }

  getLocation(location: string): AxiosRequestConfig {
    return {
      method: "GET",
      url: `${this.receivingBaseUrl}/locations/${location}`,
      headers: {
        "WMT-CorrelationId": uuid(),
      },
      timeout: this.timeout,
    };
  }

  postTwoTierTagForContainer(tag:PostTagForContainer): AxiosRequestConfig {
    return {
      method: "POST",
      url: `${this.inventoryBaseUrl}/inventory/inventories/containers/containerTag`,
      headers: {
        "WMT-UserId": "System",
        "facilityCountryCode": tag.facilityCode,
        "facilityNum": tag.facilityNumber
      },
      data: {
        trackingId: tag.containerId,
        tags: tag.tags
      }
  }
}

  getContainerDetailsByLocationAndStatus(request: getContainerDetailsRequest): AxiosRequestConfig {
    return {
      method: "GET",
      url: `${this.inventoryBaseUrl}/inventory/inventories/containers/searchByLocationAndStatus?containerStatus=${request.status}&location=${request.location}`,
      headers: {
        "WMT-CorrelationId": request.correlationId
      }
    }
  }
}

export const apiHandler = new Api();
