import { useCallback, useState } from "react";

import KeyboardIcon from "@mui/icons-material/Keyboard";
import WarningAmberIcon from "@mui/icons-material/WarningAmber";
import Backdrop from "@mui/material/Backdrop";
import CircularProgress from "@mui/material/CircularProgress";
import IconButton from "@mui/material/IconButton";

import { BottomDrawer } from "../components/bottom-drawer/bottom-drawer";
import { generateBaseUrls } from "../constants/baseUrls";
import { AlertContext, useAlertContextDefaults } from "../contexts/alert";
import { GlobalsContext, useGlobalsContextDefaults } from "../contexts/globals";
import {
  KeyboardContext,
  useKeyboardContextDefaults,
} from "../contexts/keyboard";
import { TitleContext, useTitleContextDefaults } from "../contexts/title";
import { Router } from "../router/router";
import { MyAppsData } from "../types/my-apps";
import { apiHandler } from "../utilities/api-handler";
import {
  appLogger,
  initializeAppLogger,
} from "../utilities/app-logs/app-logger";
import { GlobalScanner } from "../utilities/global-scanner/global-scanner";
import { useMyApps } from "../utilities/my-apps/useMyApps";
import { printingHandler } from "../utilities/printing/printing-handler";
import { GlobalApiStatus } from "./api-status/global-api-status";
import { CloseToteModal } from "./close-tote/close-tote-modal";
import { GlobalKeyboardModal } from "./global-keyboard/global-keyboard-modal";
import { Header } from "./header/header";
import { LeftDrawer } from "./left-drawer/left-drawer";
import { allowedStatusHandler } from "./scan-upc/allowed-status";

const { cloudPrintingUrl: CLOUD_PRINTING_URL } = generateBaseUrls();
interface AppProps {
  mockMyApps?: boolean;
  mockPrinting?: boolean;
}
export const App = ({ mockMyApps, mockPrinting }: AppProps) => {
  const [alertContextProviderDefaults, alerts] = useAlertContextDefaults();
  const [keyboardContextProviderDefaults, keyboard, setKeyboard] =
    useKeyboardContextDefaults();
  const [globalsContextProviderDefaults, globals] = useGlobalsContextDefaults();
  const [titleContextProviderDefaults] = useTitleContextDefaults();
  const [leftDrawerOpen, setLeftDrawerOpen] = useState(false);
  const [bottomDrawerOpen, setBottomDrawerOpen] = useState(false);

  const myAppsSuccessCallback = useCallback(
    (myAppsData: MyAppsData) => {
      const { cloudPrintingUrl, allowedPoLineStatus, allowedPoStatus } =
        myAppsData.dynamicConfig;

      // initialize printer
      printingHandler.init({
        cloudPrintingUrl: cloudPrintingUrl || CLOUD_PRINTING_URL,
        dcNumber: `${myAppsData.businessUnitId}`,
        countryCode: myAppsData.countryCode,
        userId: myAppsData.userId,
        mockPrinting,
      });

      // initialize apis
      apiHandler.init(myAppsData);

      // initialize allowed po poline status
      allowedStatusHandler.init({
        allowedPoStatus: allowedPoStatus || ["ACTV", "CNCL"],
        allowedPoLineStatus: allowedPoLineStatus || ["ACTIVE", "CANCELLED"],
      });

      // initialize app logger
      initializeAppLogger(myAppsData);
    },
    // eslint-disable-next-line
    [mockPrinting]
  );

  const {
    data: myAppsData,
    error: myAppsError,
    loading: myAppsLoading,
  } = useMyApps({ onSuccess: myAppsSuccessCallback, mockMyApps });

  const onKeyBoardIconClick = useCallback(() => {
    appLogger.action("App: global keyboard clicked");
    setKeyboard({ ...keyboard, open: true });
  }, [setKeyboard, keyboard]);

  const onToggleBottomDrawer = useCallback(() => {
    appLogger.action("App: warning button clicked");
    setBottomDrawerOpen(!bottomDrawerOpen);
  }, [bottomDrawerOpen, setBottomDrawerOpen]);

  if (myAppsError) {
    console.error("Error with My-Apps", myAppsError);
    return (
      <h3>
        Error with My Apps. Please re-login, if error persists please contact
        support.
      </h3>
    );
  }

  return (
    <>
      <GlobalsContext.Provider value={globalsContextProviderDefaults}>
        <AlertContext.Provider value={alertContextProviderDefaults}>
          <KeyboardContext.Provider value={keyboardContextProviderDefaults}>
            <TitleContext.Provider value={titleContextProviderDefaults}>
              <GlobalScanner
                disabled={
                  alerts.loading || keyboard.open || globals.disableScanner
                }
              />
              <Header
                openLeftDrawer={() => setLeftDrawerOpen(true)}
                actions={[
                  <IconButton
                    key="warning"
                    edge="start"
                    color="inherit"
                    aria-label="warning"
                    onClick={onToggleBottomDrawer}
                    sx={{ backgroundColor: "#223452", mr: 6 }}
                  >
                    <WarningAmberIcon fontSize="large" />
                  </IconButton>,
                  <IconButton
                    key="keyboard"
                    edge="start"
                    color="inherit"
                    aria-label="keyboard"
                    onClick={onKeyBoardIconClick}
                    sx={{ backgroundColor: "#223452" }}
                    disabled={keyboard.disabled}
                  >
                    <KeyboardIcon fontSize="large" />
                  </IconButton>,
                ]}
              />
              <LeftDrawer
                myAppsData={myAppsData}
                open={leftDrawerOpen}
                onClose={() => setLeftDrawerOpen(false)}
              />
              <GlobalApiStatus />
              <Router />
              <GlobalKeyboardModal />
              <CloseToteModal />
              <Backdrop
                sx={{
                  color: "#fff",
                  zIndex: (theme) => theme.zIndex.drawer + 1,
                }}
                open={myAppsLoading}
              >
                <CircularProgress color="inherit" />
              </Backdrop>
              <BottomDrawer
                open={bottomDrawerOpen}
                onClose={() => setBottomDrawerOpen(false)}
              />
            </TitleContext.Provider>
          </KeyboardContext.Provider>
        </AlertContext.Provider>
      </GlobalsContext.Provider>
    </>
  );
};
