import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import Box from "@mui/material/Box";
import Chip from "@mui/material/Chip";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";

export enum LogType {
  ACTION = "ACTION",
  INFO = "INFO",
  ERROR = "ERROR",
}

export interface Log {
  id: string;
  type: LogType;
  title: string;
  description?: string;
  extras?: string[];
  backgroundColor?: string;
  color?: string;
}

/**
 *
 * @param type LogType
 * @returns color string based on LgType
 */
const getTypeColor = (type: LogType) => {
  if (type === LogType.INFO) {
    return "darkcyan";
  } else if (type === LogType.ERROR) {
    return "darkred";
  }
};

interface VisualizerProps {
  logs: Log[];
}
export const Visualizer = ({ logs }: VisualizerProps) => {
  return (
    <>
      {logs.map((log) => {
        if (!log.description) {
          return (
            <Paper
              elevation={1}
              key={log.id}
              sx={{
                mt: 1,
                mb: 1,
                borderRadius: "1px",
                color: log.color || getTypeColor(log.type),
                backgroundColor: log.backgroundColor,
              }}
            >
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                  pb: 1.5,
                  pt: 1.5,
                  pr: 2,
                  pl: 2,
                }}
              >
                {log.extras && <ExtraChips chips={log.extras} />}
                <Typography
                  variant="subtitle1"
                  sx={{ textTransform: "capitalize" }}
                >
                  {log.title}
                </Typography>
              </Box>
            </Paper>
          );
        }
        return (
          <Accordion
            key={log.id}
            sx={{
              mt: 1,
              mb: 1,
              color: log.color || getTypeColor(log.type),
              backgroundColor: log.backgroundColor,
            }}
          >
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              id={log.id}
              sx={{ display: "flex", alignItems: "center" }}
            >
              {log.extras && <ExtraChips chips={log.extras} />}
              <Typography
                variant="subtitle1"
                sx={{ textTransform: "capitalize" }}
              >
                {log.title}
              </Typography>
            </AccordionSummary>
            <AccordionDetails sx={{ wordBreak: "break-all" }}>
              <Typography variant="body2">{log.description}</Typography>
            </AccordionDetails>
          </Accordion>
        );
      })}
    </>
  );
};

interface ExtraChipsProps {
  chips: string[];
}
export const ExtraChips = ({ chips }: ExtraChipsProps) => {
  return (
    <Box>
      {chips.map((chip) => (
        <Chip
          label={chip}
          key={chip}
          variant="outlined"
          size="small"
          sx={{ mr: 0.5, ml: 0.25 }}
        />
      ))}
    </Box>
  );
};
