import { DeliveryDocument } from "../../types/instruction";

interface Init {
  /**
   * allowedPoStatus: PO statuses that are elligible for receiving
   */
  allowedPoStatus: string[];
  /**
   * allowedPoLineStatus: PO line statuses that are elligible for receiving
   */
  allowedPoLineStatus: string[];
}

class AllowedStatus {
  private allowedPoStatus: string[] = [];
  private allowedPoLineStatus: string[] = [];

  init(init: Init) {
    this.allowedPoStatus = init.allowedPoStatus;
    this.allowedPoLineStatus = init.allowedPoLineStatus;
  }

  /**
   * Filters deliveryDocs if includes in allowedPoStatus
   * then within that subset filters doc lines if included in allowedPoLineStatus
   *
   * @param deliveryDocs DeliveryDocument[]
   * @returns Filtered DeliveryDocument[]
   */
  filterDeliveryDocuments(deliveryDocs: DeliveryDocument[]) {
    // deep copy
    const deliveryDocsCopy: DeliveryDocument[] = JSON.parse(
      JSON.stringify(deliveryDocs)
    );

    // filter delivery documents
    const filteredDeliveryDocs = deliveryDocsCopy.filter((deliveryDoc) =>
      this.allowedPoStatus.includes(deliveryDoc.purchaseReferenceStatus)
    );

    // filter delivery document lines
    filteredDeliveryDocs.forEach((deliveryDoc, index) => {
      const filteredDeliveryDocumentLines =
        deliveryDoc.deliveryDocumentLines.filter((docLine) =>
          this.allowedPoLineStatus.includes(docLine.purchaseReferenceLineStatus)
        );

      // if no po lines are within accepted satus, remove the document
      if (!filteredDeliveryDocumentLines.length) {
        filteredDeliveryDocs.splice(index, 1);
      }

      deliveryDoc.deliveryDocumentLines = [...filteredDeliveryDocumentLines];
    });

    return filteredDeliveryDocs;
  }
}

export const allowedStatusHandler = new AllowedStatus();
