import StorageService from './services/storage.service';
import UtilityService from './services/utility.service';
import { MawpEvents } from './mawp-events';
import {
  MAWP_NO_DATA_MSG,
  MAWP_DATA_SNIFF_TIMEOUT,
  MAWP_DATA_SNIFF_FREQUENCY,
  MAWP_GET_AUTHORIZED_APP_ERROR
} from './mawp-constants';
import HttpService from './services/http.service';

const myAppsWebPluginEvents = new MawpEvents();

myAppsWebPluginEvents.listen(); // attach events and listen for messages

let pollCounter = 0;
let snifferPollHandle = null;

/**
 * helper method to reset interval, flag and window opener object
 */
function resetPolling() {
  clearInterval(snifferPollHandle);
  pollCounter = 0;
}

function getApiHeader(http, data) {
  http.setRequestHeader('Content-Type', 'application/json');
  http.setRequestHeader('WMT-Token', UtilityService.getToken(data));
  http.setRequestHeader('WMT-BusinessUnitId', UtilityService.getBusinessUnitId(data));
  http.setRequestHeader('WMT-UserId', UtilityService.getUserId(data));
  http.setRequestHeader('WMT-Security-ID', UtilityService.getSecurityId(data));
}

/**
 * helper method to sniff data from My-Apps
 * @param success success callback
 * @param error error callback
 */
function dataSniffer(success, error) {
  const data = StorageService.getExternalData();

  if (data) {
    resetPolling();
    success(data);
  } else if (++pollCounter >= MAWP_DATA_SNIFF_TIMEOUT) {
    resetPolling();
    error(MAWP_NO_DATA_MSG);
  }
}

/**
 * method to extract the validate token object from the user object
 * @param data userobject received from MyApps
 */
function getValidateTokenObj(data) {
  try {
    return data.myappsuseonly.validateTokenObj;
  } catch (e) {
    return {};
  }
}

export default class MawpCore {

  /**
   * Method to get data from My-Apps
   * @returns Promise
   */
  static getData() {
    return new Promise((success, error) => {
      const data = StorageService.getExternalData();

      if (!data) {
        snifferPollHandle = setInterval(() => {
          dataSniffer(success, error);
        },
        MAWP_DATA_SNIFF_FREQUENCY);
      } else {
        success(data);
      }
    });
  }

  /**
   * method to get user products
   * @param data userobject received from My Apps
   */
  static getUserProducts(data) {
    return new Promise((success, error) => {
      const productsUrl = data ? data.productURL || '' : '';

      HttpService.get(productsUrl, getApiHeader, data)
        .then((data) => {
          success(data);
        }).catch((e) => {
          error(e);
        });
    });
  }

  /**
   * method to get user capabilities
   * @param data userobject received from My Apps
   */
  static getUserSMCapabilities(data) {
    return new Promise((success, error) => {
      const capabilitiesUrl = data ? data.capabilitiesURL || '' : '';

      HttpService.get(capabilitiesUrl, getApiHeader, data)
        .then((data) => {
          success(data);
        }).catch((e) => {
          error(e);
        });

    });
  }

  /**
   * method to validate token received from My Apps
   * @param data userobject received from My Apps
   */
  static validateSMToken(data) {
    return new Promise((success, error) => {
      const validateUrl = data ? data.validateUrl || '' : '';

      HttpService.put(validateUrl, getApiHeader, data, null).then((data) => {
        success(data);
      }).catch((e) => {
        error(e);
      });
    });
  }

  /**
   * method to get user capabilities received from RMA
   * @param data userobject received from MyApps
   */
  static getUserPingFedCapabilities(data) {
    return new Promise((success, error) => {
      success(data ? data.capabilities || '' : '');
    });
  }

  /**
   * method to validate MyApps token
   * @param data userobject received from MyApps
   */
  static validatePingFedToken(data) {
    return new Promise((success, error) => {
      const { url, header, method, body } = getValidateTokenObj(data);

      HttpService.send(url, header, method, body).then((data) => {
        success(data);
      }).catch((e) => {
        error(e);
      });
    });
  }

  /**
   * wrapper method to validate token received from My Apps
   * @param data userobject received from My Apps
   */
  static validateToken(data) {
    if (data && data.pingFed) {
      return this.validatePingFedToken(data);
    }
    return this.validateSMToken(data);
  }

  /**
   * wrapper method to get user capabilities
   * @param data userobject received from MyApps
   */
  static getUserCapabilities(data) {
    if (data && data.pingFed) {
      return this.getUserPingFedCapabilities(data);
    }
    return this.getUserSMCapabilities(data);
  }

  /**
   * method to get list of apps that user authorized to see in myapps home screen
   * @param data userobject received from MyApps
   */
  static getAuthorizedApps(data) {
    return new Promise((success, error) => {
      if (data && data.additional && data.additional.myApps) {
        success(data.additional.myApps);
      }
      error(MAWP_GET_AUTHORIZED_APP_ERROR);
    });
  }
}
