import { createContext, useState, useMemo } from "react";

interface KeyboardContextType {
  keyboard: KeyboardDefaults;
  setKeyboard: React.Dispatch<React.SetStateAction<KeyboardDefaults>>;
}

export interface KeyboardDefaults {
  disabled: boolean;
  open: boolean;
  children: JSX.Element | JSX.Element[];
}

const defaults: KeyboardDefaults = {
  disabled: false,
  open: false,
  children: [],
};

export const KeyboardContext = createContext<KeyboardContextType>({
  keyboard: { ...defaults },
  setKeyboard: () => {},
});

export const useKeyboardContextDefaults = (): [
  KeyboardContextType,
  KeyboardDefaults,
  React.Dispatch<React.SetStateAction<KeyboardDefaults>>
] => {
  const [keyboard, setKeyboard] = useState(defaults);
  const keyboardContextProviderDefaults = useMemo(
    () => ({
      keyboard,
      setKeyboard,
    }),
    [keyboard, setKeyboard]
  );
  return [keyboardContextProviderDefaults, keyboard, setKeyboard];
};
