import { useEffect } from "react";

import { MyAppsData } from "../../types/my-apps";
import { appLogger } from "../app-logs/app-logger";
import { sessionHandler } from "../session-handler";
import { gs1Parser } from "./parsers/gs1";
import { Scanner } from "./scanner";

interface GlobalScannerInterface {
  /**
   * disabled: boolean, to disable scanner
   */
  disabled?: boolean;
}

export const GlobalScanner = ({ disabled = false }: GlobalScannerInterface) => {
  const myAppsData = sessionHandler.getSessionStorage<MyAppsData>("myApps");
  const fncs = myAppsData?.dynamicConfig.allowedFnc || [];
  useEffect(() => {
    const scanner = new Scanner({
      onScan(values) {
        console.log(fncs);
        appLogger.info(
          `Scanner: on barcode scan ${values.parsed.get("1D")}`,
          values
        );
      },
      barcodeParser: (scanned) => gs1Parser(scanned, fncs),
    });
    if (!disabled) {
      scanner.addEventListeners();
    }
    return () => {
      scanner.removeEventListeners();
    };
    // eslint-disable-next-line
  }, [disabled]);

  return null;
};
