import { useMutation } from "@tanstack/react-query";
import { AxiosError, AxiosResponse } from "axios";
import { useCallback, useContext, useRef } from "react";
import { useLocation, useNavigate } from "react-router-dom";

import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";

import { AlertContext } from "../../contexts/alert";
import { GlobalsContext } from "../../contexts/globals";
import { ReceivingContainerResponse } from "../../types/containers";
import { MyAppsData } from "../../types/my-apps";
import {
  apiHandler,
  GetScannedUpcInfoRequest,
} from "../../utilities/api-handler";
import { appLogger } from "../../utilities/app-logs/app-logger";
import { axiosRequest } from "../../utilities/axios-request";
import { sessionHandler } from "../../utilities/session-handler";
import { ImageButton } from "../common/image-button/image-button";
import { NotesSvg } from "../common/images/notes";
import { allowedStatusHandler } from "../scan-upc/allowed-status";
import { styles } from "./qty-correction.styles";

export interface GetLpnInfoRequest {
  trackingId: string;
  isReEngageDecantFlow: boolean;
}

export interface ReEngageDecantNavState {
  containerInfo: ReceivingContainerResponse;
}

const getLpnInfo = (request: GetLpnInfoRequest) =>
  axiosRequest(
    apiHandler.getLpnInfo(request.trackingId, request.isReEngageDecantFlow),
    {
      description: `restart-decant-or-qty-correction: Restart Decant for ${request.trackingId} info`,
    }
  );

const getScannedUpcInfo = (getScannedUpcRequest: GetScannedUpcInfoRequest) =>
  axiosRequest(apiHandler.getScannedUpcInfo(getScannedUpcRequest), {
    description: `Qty-Correction: get delivery dcoument for Upc ${getScannedUpcRequest.upcNumber} details`,
  });

const getContainerDetails = (containerId: string) =>
  axiosRequest(apiHandler.getContainerInfo(containerId), {
    description: `Qty-Correction: container ${containerId} info`,
  });

export default function QtyCorrection() {
  const { pathname } = useLocation();
  const { globals, setGlobals } = useContext(GlobalsContext);
  const navigate = useNavigate();
  const { setAlerts } = useContext(AlertContext);
  const lpnInfo = useRef(null);

  const myAppsData = sessionHandler.getSessionStorage<MyAppsData>("myApps");
  const enableQtyCorrection =
    myAppsData?.dynamicConfig.enableQtyCorrection || false;
  const enableReceivingLpn =
    myAppsData?.dynamicConfig.enableReceivingLpn || false;

  const getGdmDeliveryDocsSuccess = (response: AxiosResponse) => {
    setAlerts({ success: "Container found" });

    const allowedDeliveryDocs = allowedStatusHandler.filterDeliveryDocuments(
      response.data
    );

    /**
     * @todo this will potentially show wron item details, but doing it as temp fix
     * ideally there should be a PO selection screen.
     */
    if (allowedDeliveryDocs.length) {
      // navigate to enter-quntity route
      appLogger.action("Qty-Correction: navigate to item-quantity page");
      navigate("/update-item-quantity", {
        state: {
          lpnInfo: lpnInfo.current,
          gdmDeliveryDocuments: allowedDeliveryDocs,
          containerId: globals.trackingId,
        },
      });
    } else {
      setAlerts({ error: "No POs are available" });
      appLogger.error("Qty-Correction: no po's availaible for scanned upc", {});
    }
  };

  const getGdmDeliveryDocsError = (error: AxiosError<any>) => {
    setAlerts({
      error:
        (error?.response?.data.description &&
          error?.response?.data.description[0]) ||
        error?.message ||
        "Qty-Correction error",
    });
  };

  const { mutate: scannedUpcInfo } = useMutation(getScannedUpcInfo, {
    onSuccess: (response) =>
      getGdmDeliveryDocsSuccess(response as AxiosResponse),
    onError: (error) => getGdmDeliveryDocsError(error as AxiosError<any>),
  });

  const getLpnDataSuccess = (response: AxiosResponse) => {
    lpnInfo.current = response.data;
    scannedUpcInfo({
      deliveryNumber: `${response.data.deliveryNumber}`,
      upcNumber: `${response.data.containerInventoryList[0].itemUPC}`,
    });
  };

  const getLpnDataError = useCallback(
    (error: AxiosError<any>) => {
      appLogger.error("Qty-Correction: LPN request error", error);
      console.error("Error: LPN error", error?.response?.data);
      setAlerts({
        error:
          error?.response?.data?.errorMessage ||
          error?.message ||
          "Qty-Correction error",
      });
    },
    [setAlerts]
  );

  const getContainerIdSuccess = useCallback(
    (response: AxiosResponse) => {
      setAlerts({ success: "LPN found" });
      // navigate to scan upc page with data as state
      appLogger.action(
        "restart-decant-or-qty-correction: navigate to breakpack page"
      );
      navigate("/update-item-quantity", {
        state: {
          containerId: globals.trackingId,
          containerInfo: response.data,
        },
      });
    },
    [setAlerts, navigate, globals.trackingId]
  );

  const getContainerIdError = useCallback(
    (error: AxiosError<any>) => {
      setAlerts({
        error:
          error?.response?.data?.errorMessage ||
          error?.message ||
          "Restart Decant error",
      });
    },
    [setAlerts]
  );

  const { mutate: QtyCorrectionLpnId } = useMutation(getLpnInfo, {
    onSuccess: (response) => getContainerIdSuccess(response as AxiosResponse),
    onError: (error) => getContainerIdError(error as AxiosError<any>),
  });

  const { mutate: receiveContainerId } = useMutation(getContainerDetails, {
    onSuccess: (response) => getLpnDataSuccess(response as AxiosResponse),
    onError: (error) => getLpnDataError(error as AxiosError<any>),
  });

  const onClickQtyCorrection = useCallback(() => {
    appLogger.action("Bottom-Drawer: Qty Correction button clicked");
    const trackingId = globals.trackingId;
    if (pathname !== "/breakpack") {
      setGlobals({ ...globals, trackingId: "" });
      if (!enableQtyCorrection) {
        setGlobals({
          ...globals,
          disableQuantityCorrection: false,
          trackingId: "",
        });
      }
      if (enableReceivingLpn) {
        navigate("/restart-decant-or-qty-correction", {
          state: {
            isReEngageDecantFlow: false,
          },
        });
      } else {
        navigate("/scan-lpn");
      }
    } else {
      setAlerts({ loading: true });
      if (enableReceivingLpn) {
        QtyCorrectionLpnId({
          trackingId: trackingId,
          isReEngageDecantFlow: false,
        });
      } else {
        receiveContainerId(trackingId);
      }
    }
  }, [
    enableReceivingLpn,
    pathname,
    navigate,
    setAlerts,
    receiveContainerId,
    globals,
    setGlobals,
    QtyCorrectionLpnId,
    enableQtyCorrection,
  ]);

  return (
    <>
      <ImageButton
        styles={styles.imageButton}
        onClick={onClickQtyCorrection}
        disabled={
          ["/update-item-quantity", "/split-quantity"].includes(pathname) ||
          (globals.disableQuantityCorrection && pathname === "/breakpack")
        }
      >
        <Box sx={{ m: 2 }}>
          <NotesSvg />
          <Typography
            gutterBottom
            variant="body1"
            component="div"
            sx={{ color: "black" }}
          >
            Qty Correction
          </Typography>
        </Box>
      </ImageButton>
    </>
  );
}
