import { useContext, useCallback, memo } from "react";

import Backdrop from "@mui/material/Backdrop";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";

import { GlobalsContext } from "../../contexts/globals";
import { CloseTote } from "./close-tote";
import { styles } from "./close-tote.styles";

/**
 * modal to render Close Tote Function
 */
export const CloseToteModal = memo(() => {
  const { globals, setGlobals } = useContext(GlobalsContext);

  const handleClose = useCallback(() => {
    setGlobals({ ...globals, closeToteModal: false });
  }, [globals, setGlobals]);

  return (
    <Modal
      aria-labelledby="close-tote-modal-title"
      aria-describedby="close-tote-modal--description"
      open={globals.closeToteModal}
      onClose={handleClose}
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
      }}
    >
      <Box sx={styles.modalContent}>
        <CloseTote />
      </Box>
    </Modal>
  );
});
CloseToteModal.displayName = "CloseToteModal";
