import { createContext, useState, useMemo } from "react";

interface TitleContextType {
  title: TitleDefaults;
  setTitle: React.Dispatch<React.SetStateAction<TitleDefaults>>;
}

export interface TitleDefaults {
  children: JSX.Element | null;
}

const defaults: TitleDefaults = {
  children: null,
};

export const TitleContext = createContext<TitleContextType>({
  title: { ...defaults },
  setTitle: () => { },
});

export const useTitleContextDefaults = (): [
  TitleContextType,
  TitleDefaults,
  React.Dispatch<React.SetStateAction<TitleDefaults>>
] => {
  const [title, setTitle] = useState(defaults);
  const titleContextProviderDefaults = useMemo(
    () => ({
      title,
      setTitle,
    }),
    [title, setTitle]
  );
  return [titleContextProviderDefaults, title, setTitle];
};
