import { useCallback, useState, useEffect, memo, useContext } from "react";
import { useNavigate } from "react-router-dom";

import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import AssignmentIcon from "@mui/icons-material/Assignment";
import PrintIcon from "@mui/icons-material/Print";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import Typography from "@mui/material/Typography";

import packageJson from "../../../package.json";
import { GlobalsContext } from "../../contexts/globals";
import { MyAppsData } from "../../types/my-apps";
import { appLogger } from "../../utilities/app-logs/app-logger";
import { printingHandler } from "../../utilities/printing/printing-handler";
import { useHandlePageRefresh } from "../../utilities/useHandlePageRefresh";
import { ScanCartNavState } from "../scan-cart/scan-cart";
import { styles } from "./left-drawer.styles";

interface LeftDrawerProps {
  open: boolean;
  onClose: () => void;
  /**
   * myAppsData: My Apps data object
   */
  myAppsData: MyAppsData | null;
}
export const LeftDrawer = memo(
  ({ open, onClose, myAppsData }: LeftDrawerProps) => {
    const navigate = useNavigate();
    const bottomDrawNavState = useHandlePageRefresh<ScanCartNavState>();
    const { globals, setGlobals } = useContext(GlobalsContext);
    const [selectedPrinter, setSelectedPrinter] = useState<string | null>(null);

    const onClickPrinter = useCallback(async () => {
      appLogger.action("Left-Drawer: clicked printer");
      setGlobals({ ...globals, disableScanner: true });
      try {
        await printingHandler.selectPrinter();
        setGlobals({ ...globals, disableScanner: false });
      } catch (err) {
        appLogger.error(
          "Left-Drawer: error while displaying printer list",
          err as object
        );
        console.error("Error while displaying printer list", err);
        setGlobals({ ...globals, disableScanner: false });
      }
    }, [globals, setGlobals]);

    const onClickLogs = useCallback(() => {
      appLogger.action("Left-Drawer: clicked logs");
      navigate("/app-logs", {
        state: {
          ...bottomDrawNavState,
        },
      });
    }, [navigate, bottomDrawNavState]);

    useEffect(() => {
      const getSelectedPrinter = async () => {
        try {
          const getSelectedPrinter = await printingHandler.getSelectedPrinter();
          if (getSelectedPrinter) {
            setSelectedPrinter(getSelectedPrinter.printerName);
          } else {
            appLogger.error("Left-Drawer: no selected printer", {});
            console.error("No selected printer");
          }
        } catch (err) {
          appLogger.error("Left-Drawer: error getting selected printer", {});
          console.error("Error getting selected printer", err);
        }
      };
      if (open) {
        getSelectedPrinter();
      }
    }, [open]);

    return (
      <Drawer anchor="left" open={open} onClose={onClose}>
        <Box sx={styles.content} onClick={onClose} onKeyDown={onClose}>
          <List>
            <ListItem sx={{ pb: 2 }}>
              <ListItemIcon>
                <AccountCircleIcon />
              </ListItemIcon>
              <ListItemText
                primary={`${myAppsData?.firstName} ${myAppsData?.lastName}`}
                secondary={
                  <>
                    <Typography
                      sx={{ display: "inline" }}
                      component="span"
                      variant="body2"
                      color="text.primary"
                    >
                      {myAppsData?.userId}
                    </Typography>
                    {` — ${myAppsData?.businessUnitId}`}
                  </>
                }
              />
            </ListItem>
            <ListItem button onClick={onClickPrinter}>
              <ListItemIcon>
                <PrintIcon />
              </ListItemIcon>
              <ListItemText primary="Printer" secondary={selectedPrinter} />
            </ListItem>
            <ListItem button onClick={onClickLogs}>
              <ListItemIcon>
                <AssignmentIcon />
              </ListItemIcon>
              <ListItemText primary="Logs" />
            </ListItem>
          </List>
          <List>
            <ListItem>
              <ListItemText
                primary={`v${packageJson.version}`}
                sx={{ color: "gray" }}
              />
            </ListItem>
          </List>
        </Box>
      </Drawer>
    );
  }
);
LeftDrawer.displayName = "LeftDrawer";
