import { useContext, memo, useCallback, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";

import CloseIcon from "@mui/icons-material/Close";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";

import { GlobalsContext } from "../../contexts/globals";
import { MyAppsData } from "../../types/my-apps";
import { appLogger } from "../../utilities/app-logs/app-logger";
import { sessionHandler } from "../../utilities/session-handler";
import { useHandlePageRefresh } from "../../utilities/useHandlePageRefresh";
import { ImageButton } from "../common/image-button/image-button";
import { RestartDecantSvg } from "../common/images/restart-decant";
import { ToteSvg } from "../common/images/tote";
import QtyCorrection from "../qty-correction/qty-correction";
import { ScanCartNavState } from "../scan-cart/scan-cart";
import { styles } from "./bottom-drawer.styles";

interface BottomDrawerProps {
  open: boolean;
  onClose: () => void;
}
export const BottomDrawer = memo(({ open, onClose }: BottomDrawerProps) => {
  const bottomDrawNavState = useHandlePageRefresh<ScanCartNavState>();
  const { globals, setGlobals } = useContext(GlobalsContext);
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const myAppsData = sessionHandler.getSessionStorage<MyAppsData>("myApps");
  const reEngageDecantFlow =
    myAppsData?.dynamicConfig.reEngageDecantFlow || false;

  const onClickCloseTote = useCallback(() => {
    appLogger.action("Bottom-Drawer: close tote button clicked");
    setGlobals({ ...globals, closeToteModal: true });
  }, [globals, setGlobals]);

  const onClickRestartDecant = useCallback(() => {
    appLogger.action("Bottom-Drawer: Restart Decant button clicked");
    navigate("/restart-decant-or-qty-correction", {
      state: {
        ...bottomDrawNavState,
        isReEngageDecantFlow: true,
      },
    });
  }, [navigate, bottomDrawNavState]);

  useEffect(() => {
    // reset multi sku if on root page or scan-dock-tag page
    if (["/", "/scan-dock-tag-or-tcl"].includes(pathname)) {
      setGlobals({
        ...globals,
        multiSku: false,
      });
    }
    // eslint-disable-next-line
  }, [pathname]);

  return (
    <Drawer
      anchor="bottom"
      open={open}
      onClose={onClose}
      PaperProps={{ sx: styles.paper }}
    >
      <Box sx={styles.titleContainer}>
        <Typography gutterBottom variant="h5" component="div">
          Exception Menu
        </Typography>
        <IconButton aria-label="close" onClick={onClose}>
          <CloseIcon fontSize="large" />
        </IconButton>
      </Box>
      <Box sx={{ display: "flex" }} onClick={onClose} onKeyDown={onClose}>
        <ImageButton styles={styles.imageButton} onClick={onClickCloseTote}>
          <Box sx={{ m: 2 }}>
            <ToteSvg height={128} width={128} color="black" />
            <Typography
              gutterBottom
              variant="body1"
              component="div"
              sx={{ color: "black" }}
            >
              Close Carton
            </Typography>
          </Box>
        </ImageButton>
        <QtyCorrection />
        {reEngageDecantFlow && (
          <ImageButton
            styles={styles.imageButton}
            onClick={onClickRestartDecant}
            disabled={["/breakpack", "/split-quantity", "/"].includes(pathname)}
          >
            <Box sx={{ m: 2 }}>
              <RestartDecantSvg />
              <Typography
                gutterBottom
                variant="body1"
                component="div"
                sx={{ color: "black", mt: 2 }}
              >
                Restart Decant
              </Typography>
            </Box>
          </ImageButton>
        )}
      </Box>
    </Drawer>
  );
});
BottomDrawer.displayName = "BottomDrawer";
