import { createContext, useState, useMemo } from "react";

interface GlobalsContextType {
  globals: GlobalDefaults;
  setGlobals: React.Dispatch<React.SetStateAction<GlobalDefaults>>;
}

interface GlobalDefaults {
  multiSku: boolean;
  closeToteModal: boolean;
  disableScanner: boolean;
  trackingId: string;
  disableQuantityCorrection: boolean;
  previouslyScannedDataList: string[];
}

const defaults: GlobalDefaults = {
  multiSku: false,
  closeToteModal: false,
  disableScanner: false,
  trackingId: "",
  disableQuantityCorrection: false,
  previouslyScannedDataList: [],
};

export const GlobalsContext = createContext<GlobalsContextType>({
  globals: { ...defaults },
  setGlobals: () => {},
});

export const useGlobalsContextDefaults = (): [
  GlobalsContextType,
  GlobalDefaults,
  React.Dispatch<React.SetStateAction<GlobalDefaults>>
] => {
  const [globals, setGlobals] = useState(defaults);
  const globalContextProviderDefaults = useMemo(
    () => ({
      globals,
      setGlobals,
    }),
    [globals, setGlobals]
  );
  return [globalContextProviderDefaults, globals, setGlobals];
};
