import { useState, useCallback, useMemo } from "react";

import DownloadIcon from "@mui/icons-material/Download";
import Box from "@mui/material/Box";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";

import { SearchBar } from "../../components/common/search-bar/search-bar";
import { SelectInput } from "../../components/common/select-input/select-input";
import { SubHeaderAppBar } from "../../components/common/sub-header/sub-header-app-bar";
import { saveAsTextFile } from "../download/downloadFile";
import { Log as LoggerLog, getTimeStamp } from "../logger/logger";
import { Log as VisualizerLog, LogType } from "../logger/visualizer";
import { Visualizer } from "../logger/visualizer";
import { appLogger } from "./app-logger";
import { styles } from "./app-logs.styles";

export const INFINITY = "Infinity";
export const LIMIT_OPTIONS = ["250", "500", INFINITY];

/**
 * Based on prperties on Log, return the LogType
 *
 * @param log Logger lo
 * @returns
 */
const getlogType = (log: LoggerLog): LogType => {
  if (log.hasOwnProperty("info")) {
    return LogType.INFO;
  } else if (log.hasOwnProperty("error")) {
    return LogType.ERROR;
  } else {
    return LogType.ACTION;
  }
};

/**
 *
 * @description Across the app, action are logged using `file-name: title syntax`
 * so, splitted that above string to file name and title, to display in visualizer
 * @param log logger log
 * @returns
 */
const getDetails = (log: LoggerLog) => {
  const splitTitle = log.action.split(":");
  return {
    title: splitTitle[1].trim(),
    extras: [log.timeStamp, splitTitle[0]],
  };
};

/**
 *
 * @param logs Logs from logger stack
 * @returns array of logs accepted by visuaizer
 */
const convertAppLogsToVisualizerLogs = (logs: LoggerLog[]): VisualizerLog[] => {
  const visualizerLogs: VisualizerLog[] = [];
  logs.forEach((log) => {
    const details = getDetails(log);
    visualizerLogs.push({
      id: `${log.action} - ${log.timeStamp}`,
      type: getlogType(log),
      title: details.title,
      description: log.info || log.error,
      extras: details.extras,
    });
  });
  return visualizerLogs;
};

export default function AppLogs() {
  // convert and reorder by descending timestamp
  const logs = useMemo(
    () =>
      convertAppLogsToVisualizerLogs(appLogger.getLogStack())
        .slice(0)
        .reverse(),
    []
  );
  const stackSize = useMemo(() => appLogger.getLogStatckSize().toString(), []);

  const [appLogs, setAppLogs] = useState(logs);
  const [selectInputValue, setSelectInputValue] = useState(stackSize);

  const onSearch = useCallback(
    (searchVal: string) => {
      let result = filterResults(searchVal, appLogs);
      setAppLogs(result);
    },
    [appLogs]
  );

  const onSelectLimit = useCallback(
    (value: string) => {
      if (value !== INFINITY) {
        appLogger.setLogStackSize(parseInt(value));
      } else {
        appLogger.setLogStackSize(Infinity);
      }
      setSelectInputValue(value);
      // get new sliced logs after changing the limit
      const getChangedSizeLogs = convertAppLogsToVisualizerLogs(
        appLogger.getLogStack()
      )
        .slice(0)
        .reverse();
      setAppLogs(getChangedSizeLogs);
    },
    [setSelectInputValue]
  );

  const onClickDownload = useCallback(() => {
    saveAsTextFile(JSON.stringify(appLogs), getTimeStamp());
  }, [appLogs]);

  return (
    <>
      <SubHeaderAppBar>
        <Box sx={styles.filtersContainer}>
          <Box sx={styles.selectContainer}>
            <SelectInput
              label="Limit"
              value={selectInputValue}
              options={LIMIT_OPTIONS}
              onSelect={onSelectLimit}
            />
            <IconButton
              color="inherit"
              aria-label="download"
              onClick={onClickDownload}
              sx={{ ml: 1 }}
            >
              <DownloadIcon fontSize="large" />
            </IconButton>
          </Box>

          <SearchBar onSubmit={onSearch} />
        </Box>
      </SubHeaderAppBar>
      <Box sx={{ m: 1 }}>
        {!logs.length ? (
          <Box sx={styles.noLogs}>
            <Typography variant="h6">No application logs</Typography>
          </Box>
        ) : (
          <Visualizer logs={appLogs} />
        )}
      </Box>
    </>
  );
}

/**
 * Filters the value in title and description and returns results
 *
 * @param searchVal Value to be searched
 * @param appLogs Array of visualizer logs in which to be searched
 * @returns Array of matched results
 */
const filterResults = (searchVal: string = "", appLogs: VisualizerLog[]) => {
  if (!searchVal) {
    return appLogs;
  }
  return appLogs.filter(
    (log) =>
      log.title.toLowerCase().includes(searchVal.toLowerCase()) ||
      log.description?.toLowerCase().includes(searchVal.toLowerCase())
  );
};
