import axios, { AxiosError, AxiosResponse, AxiosRequestConfig } from "axios";

import packageJson from "../../../package.json";
import { MyAppsData } from "../../types/my-apps";
import { PrintRequestResponse } from "../../types/printing";
import { apiHandler } from "../api-handler";
import { ScannerValues } from "../global-scanner/scanner";
import { Logger, Log } from "../logger/logger";
import { LIMIT_OPTIONS, INFINITY } from "./app-logs";
import { HeavyLiftLabel } from "../../types/heavyLiftLablel";

const STACK_SIZE = LIMIT_OPTIONS[0];

/**
 * Logger for this application
 */
export class AppLogger extends Logger {
  init(log: Log, callback: (log: Log) => void, stackSize = STACK_SIZE) {
    // param has to be included in the LIMIT_OPTIONS
    const size = LIMIT_OPTIONS.includes(stackSize) ? stackSize : STACK_SIZE;
    // handling infinity
    const ss = size === INFINITY ? Infinity : parseInt(size);
    super.setLogStackSize(ss);
    super.init(log, callback);
  }

  info(
    action: string,
    info:
      | AxiosResponse
      | AxiosRequestConfig
      | PrintRequestResponse[]
      | ScannerValues
      | HeavyLiftLabel
  ) {
    if ("data" in info && "config" in info) {
      // if info is api response type
      super.info(action, { headers: info.config.headers, data: info.data });
    } else if ("headers" in info) {
      // if info is api request type
      super.info(action, {
        ...info,
        headers: {
          ...axios.defaults.headers.common,
          ...info.headers,
        },
      });
    } else if ("clientId" in info) {
      // means print requuest
      super.info(action, info);
    } else if ("parsed" in info && "scanned" in info) {
      // means barcode scanned
      super.info(action, {
        scanned: info.scanned,
        parsed: map_to_object(info.parsed),
      });
    } else {
      super.info(action, info);
    }
  }

  error(action: string, error: AxiosError | object) {
    //need to check for types as in operation in string throws error
    if (typeof error !== "string" && "message" in error) {
      // if error is an error from api
      super.error(action, {
        headers: error.response?.config?.headers,
        message: error.message,
        data: error.response?.data,
      });
    } else {
      super.error(action, error);
    }
  }
}

export const appLogger = new AppLogger();

export const initializeAppLogger = (myAppsData: MyAppsData) => {
  const {
    allowedLoggerLogTypes = ["info", "error"],
    splunkToken,
    appLogsStackSize = STACK_SIZE,
  } = myAppsData.dynamicConfig;

  appLogger.init(
    {
      action: "Logger initiated",
      timeStamp: "",
      userInfo: `${myAppsData.userId} ${myAppsData.countryCode}-${myAppsData.businessUnitId}`,
      app: `Receiving Web ${packageJson.version}`,
    },
    async (log) => {
      // by default for external logging only pass info or error data objects
      // passing all user actions might overload the api calls
      if (
        splunkToken &&
        allowedLoggerLogTypes.find((type) => log.hasOwnProperty(type))
      ) {
        try {
          const instance = axios.create();
          // delete common headers for this instance.
          delete instance.defaults.headers.common["facilityCountryCode"];
          delete instance.defaults.headers.common["facilityNum"];
          delete instance.defaults.headers.common["WMT-UserId"];

          await instance(apiHandler.postLoggerLogs(log, splunkToken));
        } catch (err) {
          console.error("error during splunk event collector", err);
        }
      }
    },
    appLogsStackSize?.toString()
  );
};

/**
 * Convert a `Map` to a standard
 * JS object recursively.
 * https://gist.github.com/davemackintosh/3b9c446e8681f7bbe7c5
 *
 * @param {Map} map to convert.
 * @returns {Object} converted object.
 */
function map_to_object(map: Map<any, any>) {
  const out = Object.create(null);
  map.forEach((value, key) => {
    if (value instanceof Map) {
      out[key] = map_to_object(value);
    } else {
      out[key] = value;
    }
  });
  return out;
}
