/**
 * Printing integration guide.
 * {@link https://collaboration.wal-mart.com/display/PRTALBL/LPAAS+web+component+integration+guide Printing Integration}.
 */
import {
  _print_request_response_data_mock_,
  _selected_printer_response_data_mock,
} from "../../_mocks_/plugins/printing";
import { PrintJob } from "../../types/instruction";
import {
  PrintRequestResponse,
  SelectedPrinterResponse,
  showSelectPrinterComponentResponse,
} from "../../types/printing";
import { axiosRequest } from "../axios-request";
import { apiHandler } from "../api-handler";

// @ts-ignore
require("@walmart/printing-ui-component/src/www/lpaasUI_web");

// since printing library doesnt provide any types, setting it to our neccesity.
declare global {
  interface Window {
    AtlasPrinterUI: {
      showSelectPrinterComponent: () => Promise<showSelectPrinterComponentResponse>;
      getSelectedPrinter: () => Promise<SelectedPrinterResponse>;
      print: (prinRequest: printRequest) => Promise<PrintRequestResponse[]>;
      bulkReprint: (labelIdentifiers: string[]) => Promise<any>;
      isComponentOpen: () => boolean;
    };
  }
}

export interface Init {
  /**
   * cloudPrintingUrl: url as per the environment.
   */
  cloudPrintingUrl: string;
  /**
   * dcNumber: 5 character dc number.
   */
  dcNumber: string;
  /**
   * countryCode: 2 character country code.
   */
  countryCode: string;
  /**
   * userId: User id.
   */
  userId: string;
  /**
   * useDefaultUI: default UI which can be used to select and handle printer errors.
   */
  useDefaultUI?: boolean;
  /**
   * mockPrinting: Bypass printing requests when enabled.
   */
  mockPrinting?: boolean;
}

export interface printRequest extends PrintJob {
  userId?: string;
}

interface printByTrackingIdRequest {
  trackingIds: string[];
}

/**
 * Class representing printing api.
 */
export class Printing {
  mockPrinting = false;
  /**
   * Initializing printer api.
   *
   * @param init Object with properties needed for initializing.
   */
  init(init: Init) {
    this.mockPrinting = init.mockPrinting || false;
    try {
      // initialize printer when not mocking
      if (!this.mockPrinting) {
        (window as any).AtlasPrinterUI.init(
          init.cloudPrintingUrl,
          init.dcNumber.padStart(5, "0"),
          init.countryCode,
          init.userId,
          (init.useDefaultUI = true)
        );
      }
    } catch (err) {
      console.error("Error initializing printing", err);
    }
  }

  /**
   * Ability to select printer from the list.
   *
   * @description Show popup with list of printers. Doesnt return selected printer.
   * @returns Promise for displaying printers list.
   */
  selectPrinter() {
    try {
      if (this.mockPrinting) {
        return mockResponsePromise(true);
      }
      // displays a list of printers
      // returns selected status
      // doesnt mention anything about which printer selected
      return window.AtlasPrinterUI.showSelectPrinterComponent();
    } catch (err) {
      console.error("Error selecting printer", err);
    }
  }

  /**
   * Get selected printer info.
   *
   * @returns Promise for slected printer.
   */
  getSelectedPrinter() {
    try {
      if (this.mockPrinting) {
        return mockResponsePromise<SelectedPrinterResponse>(
          _selected_printer_response_data_mock
        );
      }
      return window.AtlasPrinterUI.getSelectedPrinter();
    } catch (err) {
      console.error("Error obtaining selected printer info", err);
    }
  }

  /**
   * Prinits labels.
   *
   * @param printRequest Request object from printing.
   * @returns Promise for print request.
   */
  print(printRequest: printRequest) {
    try {
      if (this.mockPrinting) {
        return mockResponsePromise<PrintRequestResponse[]>([
          _print_request_response_data_mock_,
        ]);
      }
      return window.AtlasPrinterUI.print(printRequest);
    } catch (err) {
      console.error("Error requesting a print label", err);
    }
  }

  /**
   * Reprint labels.
   *
   * @param labelIdentifiers List of label identifiers for reprinting.
   * @returns Promise for labels reprint reuest.
   */
  reprint(labelIdentifiers: string[]) {
    try {
      if (this.mockPrinting) {
        /**
         * @todo Find reprint response mock
         */
        return mockResponsePromise(true);
      }
      return window.AtlasPrinterUI.bulkReprint(labelIdentifiers);
    } catch (err) {
      console.error("Error requesting a reprint", err);
    }
  }

  /**
   * Checks if printer compoent is open/closed
   *
   * @returns true if printer list component is opened.
   */
  isComponentOpen() {
    try {
      if (this.mockPrinting) {
        // cause its never opened during mock
        return false;
      }
      return window.AtlasPrinterUI.isComponentOpen();
    } catch (err) {
      console.error("Error checking if component is open", err);
    }
  }

  /**
   * Checks if printer is selected
   * @description Checks if any printer is selected, if not selected displays printer list
   * and checks if user selected from the list
   */
  async isPrinterSelected() {
    try {
      // check if any printer selected
      const getSelectedPrinter =
        (await this.getSelectedPrinter()) as SelectedPrinterResponse;
      if (!getSelectedPrinter) {
        // display list of printer
        const selectedPrinter =
          (await this.selectPrinter()) as showSelectPrinterComponentResponse;
        return selectedPrinter.status === "selected";
      } else {
        return true;
      }
    } catch (err) {
      return false;
    }
  }

  /**
  * @description Accepts tracking ids and send labels for printing
*/
  printLabelsByTrackingIds(print: printByTrackingIdRequest){
    return new Promise(async (resolve, reject) => {
      try {
        postToReprintLabelFromAtlas(print).then(
          function success(response: any) {
            if (response && (response.data as printRequest)) {
              printingHandler.print(response.data as printRequest)?.then(
                function success() {
                  resolve("1 label sent to printer");
                  return;
                },
                function error() {
                  reject("error printing label");
                  return;
                }
              );
            } else {
              reject("unable to parse reprint response from atlas");
              return;
            }
          },
          function error() {
            reject("unable to get print data from atlas");
            return;
          }
        );
      } catch (error) {
        reject("Error while fetching print job request");
        return;
      }
    })
  }

}

export const printingHandler = new Printing();

/**
 *
 * @param data Promise resolves to this data
 * @returns Promise which resolves to data
 */
const mockResponsePromise = <T,>(data: T): Promise<T> => {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(data);
    }, 0);
  });
};

const postToReprintLabelFromAtlas = (body: printByTrackingIdRequest) => {
  return axiosRequest(apiHandler.postReprintLabelFromAtlas(body), {
    description: `Reprint: Reprint request to atlas`,
  });
};
