import { useCallback, memo, useContext } from "react";
import { useNavigate } from "react-router-dom";

import MenuIcon from "@mui/icons-material/Menu";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import IconButton from "@mui/material/IconButton";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";

import { TitleContext } from "../../contexts/title";
import { appLogger } from "../../utilities/app-logs/app-logger";
import { styles } from "./header.styles";

interface HeaderProps {
  openLeftDrawer: () => void;
  /**
   * actions: Optional array of elements thats need to bedisplayed at right end
   */
  actions?: JSX.Element[];
}

/**
 * Component with Header and Drawer
 * Handles open/close of drawer and click event in drawer
 */
export const Header = memo(({ openLeftDrawer, actions = [] }: HeaderProps) => {
  const navigate = useNavigate();
  const { title } = useContext(TitleContext);

  const onClickTitle = useCallback(() => {
    appLogger.action("Header: app title clicked");
    navigate("/");
  }, [navigate]);

  return (
    <AppBar position="static" sx={{ bgcolor: "primary.dark" }}>
      <Toolbar>
        <IconButton
          size="large"
          edge="start"
          color="inherit"
          aria-label="menu"
          sx={styles.iconButton}
          onClick={openLeftDrawer}
        >
          <MenuIcon fontSize="large" />
        </IconButton>
        <Box sx={{ flexGrow: 1 }}>
          {title.children || (
            <Typography
              variant="h5"
              component="div"
              sx={styles.title}
              onClick={onClickTitle}
            >
              Receiving
            </Typography>
          )}
        </Box>
        <Box>{actions}</Box>
      </Toolbar>
    </AppBar>
  );
});
Header.displayName = "Header";
