import { memo, lazy, Suspense } from "react";
import { Navigate, Route, Routes } from "react-router-dom";

import Backdrop from "@mui/material/Backdrop";
import CircularProgress from "@mui/material/CircularProgress";

const AppLogs = lazy(() => import("../utilities/app-logs/app-logs"));
const ScanCart = lazy(() => import("../components/scan-cart/scan-cart"));
const ScanTcl = lazy(() => import("../components/scan-tcl/scan-tcl"));
const DockTagOrTcl = lazy(() => import("../components/dock-tag/dock-tag"));
const ScanUpc = lazy(() => import("../components/scan-upc/scan-upc"));
const ScanUpcMultiSku = lazy(
  () => import("../components/scan-upc/scan-upc-multisku")
);
const ScanLpn = lazy(() => import("../components/scan-lpn/scan-lpn"));
const ReEngageDecantOrQtyCorrection = lazy(
  () => import("../components/re-engage-decant/re-engage-decant")
);
const PoSelection = lazy(
  () => import("../components/po-selection/po-selection")
);
const ItemQuantity = lazy(
  () => import("../components/item-quantity/item-quantity")
);
const UpdateItemQuantity = lazy(
  () => import("../components/update-item-quantity/update-item-quantity")
);
const Breakpack = lazy(() => import("../components/breakpack/breakpack"));
const SplitQuantity = lazy(
  () => import("../components/split-quantity/split-quantity")
);

export const Router = memo(() => {
  return (
    <Suspense
      fallback={
        <Backdrop
          sx={{
            color: "#fff",
            zIndex: (theme) => theme.zIndex.drawer + 1,
          }}
          open={true}
        >
          <CircularProgress color="inherit" />
        </Backdrop>
      }
    >
      <Routes>
        <Route path="app-logs" element={<AppLogs />} />
        <Route path="/" element={<ScanCart />} />
        <Route path="scan-tcl" element={<ScanTcl />} />
        <Route path="scan-lpn" element={<ScanLpn />} />
        <Route
          path="restart-decant-or-qty-correction"
          element={<ReEngageDecantOrQtyCorrection />}
        />
        <Route path="update-item-quantity" element={<UpdateItemQuantity />} />
        <Route path="scan-dock-tag-or-tcl" element={<DockTagOrTcl />} />
        <Route path="scan-upc" element={<ScanUpc />} />
        <Route path="scan-upc-multisku" element={<ScanUpcMultiSku />} />
        <Route path="po-selection" element={<PoSelection />} />
        <Route path="item-quantity" element={<ItemQuantity />} />
        <Route path="breakpack" element={<Breakpack />} />
        <Route path="split-quantity" element={<SplitQuantity />} />
        <Route path="*" element={<Navigate to="/" replace />} />
        {/* No matching route */}
        <Route
          path="*"
          element={
            <main style={{ padding: "1rem" }}>
              <p>No route found!</p>
            </main>
          }
        />
      </Routes>
    </Suspense>
  );
});
Router.displayName = "Router";
