class Session {
  /**
   * Get value for a property from session storage
   *
   * @param name name of property in session storage
   * @returns Parsed value of type T
   */
  getSessionStorage<T>(name: string) {
    const val = window.sessionStorage.getItem(name);
    return val ? (JSON.parse(val) as T) : null;
  }

  /**
   * Set session storage for a property
   *
   * @param name Name of property in session storage
   * @param value Value for that property
   */
  setSessionStorage<T>(name: string, value: T) {
    window.sessionStorage.setItem(name, JSON.stringify(value));
  }

  /**
   * Remove specific property from session storage
   *
   * @param name Name of property that needs to be removed
   */
  removeSessionStorage(name: string) {
    window.sessionStorage.removeItem(name);
  }

  /**
   * Remove all saved data from sessionStorage
   */
  clearSessionStorage() {
    window.sessionStorage.clear();
  }
}

export const sessionHandler = new Session();
