import { memo } from "react";

import Backdrop from "@mui/material/Backdrop";
import Box from "@mui/material/Box";
import CircularProgress from "@mui/material/CircularProgress";

import { CollapsableAlert } from "../common/collapsable-alert/collapsable-alert";
import { styles } from "./api-status.styles";

interface ApiStatusProps {
  success: string | null;
  error: string | null;
  loading: boolean;
  actionButtons?: JSX.Element | null;
}

/**
 * Based on the props display the content
 *
 * if loading -> display loading
 * else if error -> display error message
 * else if success -> display success message
 * else -> display nothing
 */
export const ApiStatus = memo(
  ({ success, error, loading, actionButtons }: ApiStatusProps) => {
    const elements = () => {
      if (error)
        return (
          <CollapsableAlert
            message={error}
            severity="error"
            actionButtons={actionButtons}
          />
        );
      if (success)
        return (
          <CollapsableAlert
            message={success}
            severity="success"
            timer={2000}
            actionButtons={actionButtons}
          />
        );
      return null;
    };

    return (
      <Box sx={styles.relativePosition}>
        <Box sx={styles.absolutePosition}>{elements()}</Box>
        <Backdrop
          sx={{
            color: "#fff",
            zIndex: (theme) => theme.zIndex.drawer + 1,
          }}
          open={loading}
        >
          <CircularProgress color="inherit" />
        </Backdrop>
      </Box>
    );
  }
);
ApiStatus.displayName = "ApiStatus";
