import { useEffect, useState } from "react";

import { MyAppsData } from "../../types/my-apps";
import { myAppsHandler } from "./my-apps-handler";

interface Status {
  data: null | MyAppsData;
  error: unknown;
  loading: boolean;
}

interface UseMyApps {
  /**
   * onSuccess Optional callback when my-apps is success.
   */
  onSuccess: (data: MyAppsData) => void;
  /**
   * onError Optional callback when my-apps is failed.
   */
  onError: (err: unknown) => void;
  /**
   * mockMyApps Optional mocking my-apps data.
   */
  mockMyApps: boolean;
  /**
   * redirectToMyAppsOnError If true, redirects to my-apps login page if my-apps data validation is failed.
   */
  redirectToMyAppsOnError: boolean;
  /**
   * myAppsBaseUrl Optional base url for my-apps, will use default url if not provided.
   */
  myAppsBaseUrl: string;
}

/**
 * Custtom hook for my-apps status
 *
 * @param params UseMyApps.
 * @returns Object with { data, error, loading } values while validating my-apps.
 */
export const useMyApps = ({
  onSuccess,
  onError,
  mockMyApps = false,
  redirectToMyAppsOnError = true,
  myAppsBaseUrl,
}: Partial<UseMyApps> = {}) => {
  const [status, setStatus] = useState<Status>({
    data: null,
    error: null,
    loading: false,
  });
  useEffect(() => {
    myAppsHandler.init({ mockMyApps });
    const myAppsValidation = async () => {
      setStatus({
        data: null,
        error: null,
        loading: true,
      });
      try {
        const validatedData =
          (await myAppsHandler.validateMyAppsLogin()) as MyAppsData;
        setStatus({
          data: validatedData,
          error: null,
          loading: false,
        });
        // pass the data to callback
        if (onSuccess) onSuccess(validatedData);
      } catch (err) {
        if (redirectToMyAppsOnError) {
          // redirect to my-apps
          myAppsHandler.goToMyAppsLogin(myAppsBaseUrl);
        }
        setStatus({
          data: null,
          error: err,
          loading: false,
        });
        // pass the data to callback
        if (onError) onError(err);
      }
    };
    myAppsValidation();
  }, [myAppsBaseUrl, redirectToMyAppsOnError, onSuccess, onError, mockMyApps]);

  return status;
};
