import { useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";

/**
 * Custom hook to check for navSatate.
 * @description Redirects to path if navState is not available.
 *
 * @param to Path to which page will be redirected.
 * @returns [navState]
 */
export const useHandlePageRefresh = <T,>(to = "/") => {
  const navState = useLocation().state as T | null;
  const navigate = useNavigate();

  useEffect(() => {
    if (!navState) {
      // navigate to root page
      navigate(to);
    }
  }, [navigate, to, navState]);

  return navState;
};
