import { useEffect } from "react";

import { ScannerValues } from "./global-scanner/scanner";

/**
 * Custom hook for listening to the events
 *
 * @param eventName Event name
 * @param handler Callback when event is triggered
 */
export function useEventListener<T>(
  eventName: string,
  handler: (event: Event | CustomEvent<T>) => void,
  options?: AddEventListenerOptions
) {
  useEffect(() => {
    document.addEventListener(eventName, handler, options);
    return () => {
      document.removeEventListener(eventName, handler, options);
    };
  }, [eventName, handler, options]);
}

/**
 * Custom hook for listening to 'scanned' events
 *
 * @param handler callback handler
 * @param options AddEventListenerOptions
 */
export function useScannedEventListener<T = ScannerValues>(
  handler: (event: CustomEvent<T>) => void,
  options?: AddEventListenerOptions
) {
  return useEventListener("scan", (e) => handler(e as CustomEvent), options);
}
