import React from 'react'
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import Divider from "@mui/material/Divider";
import Typography from "@mui/material/Typography";
import heavyLiftLabelImg from '../images/heavyLiftLabel.png'

import { styles } from "./dialog-box.style";

interface heavyLiftProps {
    open: boolean,
    header: String,
    content: String,
    handleConfirm: () => void
}

export default function DialogHeavyLift({
    open,
    header,
    content,
    handleConfirm
}: heavyLiftProps) {
  return (
    <Dialog 
        open={open}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        PaperProps={{ sx: styles.paper }}
    >
        <Box sx={styles.titleContainer}>
            <Typography gutterBottom variant="h4" component="div">
              <b>{header}</b>
            </Typography>
        </Box>

        <Divider />

        <DialogContent>
            <DialogContentText
              id="alert-dialog-description"
              sx={{ fontSize: "20px", color: "black" }}
            >
              {content.split('\n').map((line: String) => {
                return (
                  <div style={{"marginBottom": "15px"}}>
                    {line}
                  </div>
                )
              })}
              <Box sx={{ width: 1, display: "flex", justifyContent: "center" }}>
                <img src={heavyLiftLabelImg} alt="Heavy Label Image" style={{"height": "15vh", "width": "40vh"}}/>
              </Box>
            </DialogContentText>
        </DialogContent>

        <Divider />
          <DialogActions sx={{ m: 2 }}>
            <Button
              variant="contained"
              key="receive"
              size="large"
              sx={styles.button}
              onClick={handleConfirm}
            >
              Confirm
            </Button>
        </DialogActions>
    </Dialog>
  )
}
