import axios, { AxiosRequestConfig } from "axios";

import { appLogger } from "./app-logs/app-logger";

interface LoggingDetails {
  /**
   * description: api request description
   */
  description: string;
}

/**
 * Axios request util for interception logics
 *
 * @param config Axios request config
 * @param loggingDetails optional logging addon, Will add to logs if this param is available
 */
export const axiosRequest = (
  config: AxiosRequestConfig,
  loggingDetails?: Partial<LoggingDetails>
) => {
  if (loggingDetails) {
    appLogger.info(
      `${loggingDetails.description || "Component: api-description"} requested`,
      config
    );
  }

  return new Promise((resolve, reject) => {
    axios(config)
      .then((response) => {
        if (loggingDetails) {
          appLogger.info(
            `${
              loggingDetails.description || "Component: api-description"
            } request success`,
            response
          );
        }
        resolve(response);
      })
      .catch((error) => {
        if (loggingDetails) {
          appLogger.error(
            `${
              loggingDetails.description || "Component: api-description"
            } request error`,
            error
          );
        }
        if (process.env.NODE_ENV === "development") {
          console.error("Error: Dock Tag error", error?.response?.data);
        }
        reject(error);
      });
  });
};
