import { useState, useCallback } from "react";

import { PrintJob } from "../../types/instruction";
import { PrintRequestResponse } from "../../types/printing";
import { printingHandler } from "./printing-handler";

interface UseLazyPrintState {
  data: PrintRequestResponse[] | null;
  loading: boolean;
  error: unknown | null;
}

interface UseLazyPrintCallbacks {
  /**
   * onPrintRequestSuccess: On print request success
   */
  onPrintRequestSuccess: (printRequestResposne: PrintRequestResponse[]) => void;
  /**
   * onPrintRequestError: On print requuest failure
   */
  onPrintRequestError: (error: unknown) => void;
}

/**
 * Custom axios hook for calling print request on callback
 *
 * @param onPrintRequestSuccess Optional callback when print request is success
 * @param onPrintRequestError Optional callback when print request is failed
 * @returns [ printRequest(PrintJob), { data, error, loading, ... } ]
 * @description printRequest -> when invoked, calls the print request and updates the state.
 */
export const useLazyPrint = (
  callbacks: UseLazyPrintCallbacks
): [(printJob: PrintJob) => void, UseLazyPrintState] => {
  const [state, setState] = useState<UseLazyPrintState>({
    data: null,
    error: null,
    loading: false,
  });

  const printRequest = useCallback(
    async (printJob: PrintJob) => {
      setState({
        data: null,
        error: null,
        loading: true,
      });
      try {
        const printJobStatus = (await printingHandler.print(
          printJob
        )) as PrintRequestResponse[];
        setState({
          data: printJobStatus,
          error: null,
          loading: false,
        });
        callbacks.onPrintRequestSuccess(printJobStatus);
      } catch (err) {
        setState({
          data: null,
          error: err,
          loading: false,
        });
        callbacks.onPrintRequestError(err);
      }
    },
    [callbacks]
  );

  return [printRequest, state];
};
