import { useCallback, useState, ChangeEvent } from "react";

import KeyboardIcon from "@mui/icons-material/Keyboard";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import FormControl from "@mui/material/FormControl";
import InputAdornment from "@mui/material/InputAdornment";
import InputLabel from "@mui/material/InputLabel";
import OutlinedInput from "@mui/material/OutlinedInput";
import Typography from "@mui/material/Typography";

import { styles } from "./input-form.styles";

interface InputFormProps {
  /**
   * title: Title of the Custom Form
   */
  title?: string;
  /**
   * inputLabel: Label that needs to be shown on the input text box
   */
  inputLabel: string;
  /**
   * onConfirm: Callback when button is clicked
   */
  onConfirm: (value: string) => void;
  /**
   * buttonDisabled: disable confirm button
   */
  buttonDisabled?: boolean;
}

/**
 *
 * Component with <title>, <input>, <confirm button>
 */
export const InputForm = ({
  title,
  inputLabel,
  onConfirm,
  buttonDisabled = false,
}: InputFormProps) => {
  const [value, setValue] = useState("");

  const onChangeInput = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      setValue(event.target.value);
    },
    [setValue]
  );

  const onClickConfirm = useCallback(() => {
    onConfirm(value);
  }, [onConfirm, value]);

  return (
    <>
      {title && (
        <Typography id="transition-modal-title" variant="h6" component="h2">
          {title}
        </Typography>
      )}
      <FormControl variant="outlined" sx={styles.form}>
        <InputLabel htmlFor="input-form">{inputLabel}</InputLabel>
        <OutlinedInput
          id="input-form"
          autoComplete="off"
          value={value}
          onChange={onChangeInput}
          endAdornment={
            <InputAdornment position="end">
              <KeyboardIcon />
            </InputAdornment>
          }
          label={inputLabel}
        />
        <Box sx={styles.buttonContainer}>
          <Button
            variant="outlined"
            key="confirm"
            size="large"
            sx={styles.confirmButton}
            onClick={onClickConfirm}
            disabled={!value.trim() || buttonDisabled}
          >
            Confirm
          </Button>
        </Box>
      </FormControl>
    </>
  );
};
