import { QueryClient, QueryClientProvider } from "@tanstack/react-query";
import { ReactQueryDevtools } from "@tanstack/react-query-devtools";
import React from "react";
import ReactDOM from "react-dom/client";
import { BrowserRouter } from "react-router-dom";

import CssBaseline from "@mui/material/CssBaseline";
import { ThemeProvider } from "@mui/material/styles";

import { App } from "./components/app";
import { ErrorBoundary } from "./components/common/error-boundary/error-boundary";
import "./index.css";
import { theme } from "./styles/themes/theme";

/**
 * Path at which web page can be accessed
 *
 * in development mode localhost:3000/
 * in production mode <origin>/receiving-web/
 */
const BASE_PATH =
  process.env.NODE_ENV === "development" ? "/" : "/receiving-web";

const ENABLE_MOCKS = process.env.NODE_ENV === "development";

/**
 * Enable the worker code for mocking apis.
 */
if (false) {
  const { worker } = require("./_mocks_/apis/mock-service-worker");
  worker.start({
    // ignores unhandled requests
    onUnhandledRequest: "bypass",
  });
}

// Enable/disable the mocks
// by default mocks are enabled during dev mode.
const mockPlugins = {
  mockMyApps: ENABLE_MOCKS,
  mockPrinting: ENABLE_MOCKS,
};

// Create a react quuery client with defaults
const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      refetchOnWindowFocus: false,
      refetchOnMount: false,
      refetchOnReconnect: false,
      retry: false,
    },
  },
});

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
root.render(
  <QueryClientProvider client={queryClient}>
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <BrowserRouter basename={BASE_PATH}>
        <ErrorBoundary
          postLogs={(error) => {
            console.log("error logs", error);
          }}
        >
          <App {...mockPlugins} />
        </ErrorBoundary>
      </BrowserRouter>
    </ThemeProvider>
    <ReactQueryDevtools initialIsOpen={false} />
  </QueryClientProvider>
);
