const LOG_STACK_SIZE = 50;

export const INITIAL_LOG = {
  action: "",
  timeStamp: "",
};

export interface Log {
  action: string;
  info?: string;
  error?: string;
  timeStamp: string;
  [key: string]: string | undefined;
}

/**
 *
 */
export class Logger {
  private log: Log = INITIAL_LOG;
  private logStack: Log[] = [];
  private stackSize = LOG_STACK_SIZE;

  // this callback is used by init method
  // do not write any code inside it
  callback(log: Log) {}

  /**
   *  For initializing with initial values.
   *
   * @param log initialize the default log object structure
   * @param callback Function invoked when `.info` or `.error` called.
   */
  init(log: Log, callback: (log: Log) => void) {
    this.reset();
    this.log = { ...this.log, ...log };
    this.callback = callback;
  }

  /**
   * For logging info/success with some data
   *
   * @param action Action Name, it should be of format Title: Description
   * @param info Info object that needs to be logged
   */
  info(action: string, info: object | string) {
    this.log = {
      ...this.log,
      action,
      info: JSON.stringify(info),
      timeStamp: getTimeStamp(),
    };
    delete this.log.error;
    this.pushAndResize(this.log);
    this.callback(this.log);
  }

  /**
   * For logging errors with some data
   *
   * @param action Action Name, it should be of format Title: Description
   * @param error Error object that needs to be logged
   */
  error(action: string, error: object | string) {
    this.log = {
      ...this.log,
      action,
      error: JSON.stringify(error),
      timeStamp: getTimeStamp(),
    };
    delete this.log.info;
    this.pushAndResize(this.log);
    this.callback(this.log);
  }

  /**
   * For logging user actions
   *
   * @param action Action Name, it should be of format Title: Description
   */
  action(action: string) {
    this.log = {
      ...this.log,
      action,
      timeStamp: getTimeStamp(),
    };
    delete this.log.info;
    delete this.log.error;
    this.pushAndResize(this.log);
    this.callback(this.log);
  }

  /**
   * Reset log
   */
  reset() {
    this.log = INITIAL_LOG;
  }

  /**
   *
   * @param size Size of stack thats need to be stacked
   */
  setLogStackSize(size: number) {
    this.logStack = this.logStack.slice(-size);
    this.stackSize = size;
  }

  /**
   * Get log stack
   *
   * @returns logStack
   */
  getLogStack() {
    return this.logStack;
  }

  /**
   * Pushes to stack and resizes based on size
   *
   * @param log Log thats need to be pushed to stack
   */
  private pushAndResize(log: Log) {
    this.logStack.push(log);
    this.logStack = this.logStack.slice(-this.stackSize);
  }

  /**
   *
   * @returns stack size
   */
  getLogStatckSize() {
    return this.stackSize;
  }
}

export const logger = new Logger();

/**
 * Get Current time stamp
 *
 * @returns time stamp with format mm/dd/yyyy hh:mm:ss.ms
 */
export const getTimeStamp = () => {
  const _date = new Date();
  return (
    _date.getMonth() +
    1 +
    "/" +
    _date.getDate() +
    "/" +
    _date.getFullYear() +
    " " +
    _date.getHours() +
    ":" +
    ("0" + _date.getMinutes()).slice(-2) +
    ":" +
    ("0" + _date.getSeconds()).slice(-2) +
    "." +
    ("00" + _date.getMilliseconds()).slice(-3)
  );
};
