export const COOKIE_KEY = 'decryptKey';

let externalData = null;
let sessionData = null;

export default class StorageService {

  static getCookie(cookieKey) {
    let name = cookieKey + '=';
    let decodedCookies = decodeURIComponent(document.cookie);
    let splitCookies = decodedCookies.split(';');

    for (let i = 0; i < splitCookies.length; i++) {
      let cookie = splitCookies[i];

      while (cookie.charAt(0) === ' ') {
        cookie = cookie.substring(1);
      }
      if (cookie.indexOf(name) === 0) {
        return cookie.substring(name.length, cookie.length);
      }
    }
    return '';
  }

  static saveCookie(key) {
    let expirationDate = key.expirationDate;
    let hashKey = key.key;

    document.cookie = COOKIE_KEY + '=' + hashKey + '; expires=' + expirationDate;
  }

  static getLocalData(key) {
    return localStorage.getItem(key);
  }

  static setLocalData(key, data) {
    return localStorage.setItem(key, data);
  }

  static getExternalData() {
    return externalData;
  }

  static setExternalData(data) {
    externalData = data;
  }

  static getSessionData() {
    return sessionData;
  }

  static setSessionData(data) {
    sessionData = data;
  }
}
