import { useCallback, useContext, useEffect, useState } from "react";

import CloseIcon from "@mui/icons-material/Close";
import Alert, { AlertColor } from "@mui/material/Alert";
import Collapse from "@mui/material/Collapse";
import IconButton from "@mui/material/IconButton";

import { AlertContext } from "../../../contexts/alert";
import { styles } from "./collapsable-alert.styles";

interface CollapsableAlertProps {
  /**
   * message: Message in the alert
   */
  message: string | null;

  /**
   * actionButtons: custom action buttons to be rendered in collapsable alert
   */

  actionButtons?: JSX.Element | null;

  /**
   * severity: 'success' | 'info' | 'warning' | 'error';
   */
  severity: AlertColor;
  /**
   * timer: Time in milliseconds to close the alert after showing
   */
  timer?: number;
}

/**
 * Collapsable alert component
 *
 * By default alert stay till user closes it
 * if provided timer prop, alert auto closes after timer expires
 */
export const CollapsableAlert = ({
  actionButtons = null,
  message = null,

  severity,
  timer,
}: CollapsableAlertProps) => {
  const [show, setShow] = useState(true);
  const { setAlerts } = useContext(AlertContext);

  const onCloseAlert = useCallback(() => {
    setShow(false);
    setAlerts({ success: null, error: null });
  }, [setShow, setAlerts]);

  useEffect(() => {
    if (timer) {
      const timeOut = setTimeout(() => {
        onCloseAlert();
      }, timer);
      return () => clearTimeout(timeOut);
    }
  }, [timer, onCloseAlert]);

  return (
    <Collapse in={show}>
      {
        <Alert
          variant="filled"
          severity={severity}
          sx={styles.alert}
          action={
            actionButtons ? (
              actionButtons
            ) : (
              <IconButton
                aria-label="close"
                color="inherit"
                size="small"
                onClick={onCloseAlert}
              >
                <CloseIcon fontSize="inherit" />
              </IconButton>
            )
          }
        >
          {message}
        </Alert>
      }
    </Collapse>
  );
};
