import { useMutation } from "@tanstack/react-query";
import { AxiosError, AxiosResponse } from "axios";
import { useContext, useCallback, useState } from "react";

import Box from "@mui/material/Box";

import { GlobalsContext } from "../../contexts/globals";
import { CloseToteResponse } from "../../types/close-tote";
import { PrintRequestResponse } from "../../types/printing";
import { apiHandler } from "../../utilities/api-handler";
import { appLogger } from "../../utilities/app-logs/app-logger";
import { axiosRequest } from "../../utilities/axios-request";
import { printingHandler } from "../../utilities/printing/printing-handler";
import { useLazyPrint } from "../../utilities/printing/useLazyPrint";
import { useScannedEventListener } from "../../utilities/useEventListener";
import { ApiStatus } from "../api-status/api-status";
import { InputForm } from "../common/input-form/input-form";
import { styles } from "./close-tote.styles";
import { ToteDone } from "./tote-done";
import { ToteScan } from "./tote-scan";
import { getContainerDetailsRequest } from "../../utilities/api-handler";
import { MyAppsData } from "../../types/my-apps";
import { sessionHandler } from "../../utilities/session-handler";
import DialogHeavyLift from "../common/dialog-box/dialog-heavy-lift";
import { PrintJob } from "../../types/instruction";
import { HeavyLiftLabel, GetContainerDetailsError } from "../../types/heavyLiftLablel";
import {v4 as uuid} from "uuid";

export const CLOSETOTEACTIONS = {
  DONE: "DONE",
  SCAN_INPUT: "SCAN",
  KEYBOARD_INPUT: "KEYBOARD",
};

const putCloseToteRequest = (tote: string) =>
  axiosRequest(apiHandler.putCloseTote(tote), {
    description: `Close-Tote: close ${tote}`,
  });

const getContainerDetailsByLocationAndStatusRequest = async (request: getContainerDetailsRequest) => 
  await axiosRequest(apiHandler.getContainerDetailsByLocationAndStatus(request), {
      description: `Close Tote: Container details by location and status`
  })

interface AlertStatus {
  success: string | null;
  error: string | null;
  loading: boolean;
}

let containerWeight: number | null = null;
let trackingId: String | undefined = undefined;

export const CloseTote = () => {
  const { globals, setGlobals } = useContext(GlobalsContext);
  const [action, setAction] = useState(CLOSETOTEACTIONS.SCAN_INPUT);
  const [alerts, setAlerts] = useState<AlertStatus>({
    success: null,
    error: null,
    loading: false,
  });

  const myAppsData = sessionHandler.getSessionStorage<MyAppsData>("myApps");
  const enableHeavyLiftLabel = myAppsData?.dynamicConfig.enableHeavyLiftLabel || false;
  const heavyLiftThreshold = myAppsData?.dynamicConfig.heavyLiftThreshold || 1e9;
  const heavyLiftCorrelationId = uuid();

  const [showHeavyLiftPopUp, setShowHeavyLiftPopUp] = useState<Boolean>(false);
  const [printJobData, setPrintJobData] = useState<PrintJob | null>(null);
  
  const printInstructionPrintJobSuccess = useCallback(
    (response: PrintRequestResponse[]) => {
      appLogger.info("Close-Tote: print request success", response);
      setAlerts({
        loading: false,
        success: "Printing label. Place label on carton",
        error: null,
      });
      setAction(CLOSETOTEACTIONS.DONE);
    },
    [setAlerts]
  );

  const printInstructionPrintJobError = useCallback(
    (error: unknown) => {
      appLogger.error("Close-Tote: print request error", error as object);
      setAlerts({
        loading: false,
        error: "Error during print request",
        success: null,
      });
    },
    [setAlerts]
  );

  const [printInstructionPrintJob] = useLazyPrint({
    onPrintRequestSuccess: printInstructionPrintJobSuccess,
    onPrintRequestError: printInstructionPrintJobError,
  });

  const putCloseToteSuccess = useCallback(
    (response: AxiosResponse<CloseToteResponse>) => {
      const { labelData } = response.data;
      setPrintJobData(labelData);
      if (labelData) {
        appLogger.info("Close-Tote: print request", labelData);

        if(enableHeavyLiftLabel && containerWeight && containerWeight >= heavyLiftThreshold){
          const logInfo: HeavyLiftLabel = {
            trackingId: trackingId,
            correlationId: heavyLiftCorrelationId
          };
          appLogger.info('Heavy Lift Label Popup: Popup Displayed', logInfo)
          setShowHeavyLiftPopUp(true);
        }
        else{
          printInstructionPrintJob(labelData);
        }
        
      } else {
        appLogger.error("Close-Tote: printjob not found", {});
        setAlerts({
          loading: false,
          error: "No print job found",
          success: null,
        });
      }
    },
    [setAlerts, printInstructionPrintJob]
  );

  const putCloseToteError = useCallback((error: AxiosError<any>) => {
    setAlerts({
      loading: false,
      success: null,
      error:
        (error?.response?.data?.messages &&
          error?.response?.data?.messages[0]?.description) ||
        error?.message ||
        "Error while closing tote",
    });
  }, []);

  const { mutate: closeTote } = useMutation(putCloseToteRequest, {
    onSuccess: (response) =>
      putCloseToteSuccess(response as AxiosResponse<CloseToteResponse>),
    onError: (error) => putCloseToteError(error as AxiosError<any>),
  });

  const onClickManualEntry = useCallback(() => {
    appLogger.action("Close-Tote: manual entry button clicked");
    setAction(CLOSETOTEACTIONS.KEYBOARD_INPUT);
  }, []);

  const onDoneClick = useCallback(() => {
    appLogger.action("Close-Tote: done button clicked");
    setGlobals({ ...globals, closeToteModal: false });
  }, [globals, setGlobals]);

  const checkPrintersAndCloseTote = useCallback(
    async (value: string) => {
      const isprinterSelected = await printingHandler.isPrinterSelected();
      if (isprinterSelected) {
        setAlerts({
          success: null,
          error: null,
          loading: true,
        });
        closeTote(value);
      } else {
        setAlerts({
          loading: false,
          success: null,
          error: "No printer selected, please select a printer to continue",
        });
        appLogger.error(
          "Close-Tote: No printer selected, please select a printer to continue",
          {}
        );
      }
    }, [closeTote]
  )

  const onClickConfirm = useCallback(
    async (value: string) => {
      setAlerts({
        success: null,
        error: null,
        loading: true
      });

      if(enableHeavyLiftLabel){
        // get container weight from inventory
        getContainerDetailsByLocationAndStatusRequest({
          location: value,
          status: 4,
          correlationId: heavyLiftCorrelationId
        }).then((response: any) => {
          setAlerts({
            success: null,
            error: null,
            loading: false
          });
          const totalWeight = parseFloat(response.data?.containers[0]?.containerWeightQuantity) || null;
          containerWeight = totalWeight || 0;

          const trackingIdResponse = response.data?.containers[0]?.trackingId || undefined;
          trackingId = trackingIdResponse;
          checkPrintersAndCloseTote(value);
        }).catch((error) => {
          const logInfo: GetContainerDetailsError = {
            correlationId: heavyLiftCorrelationId,
            error: error,
            trackingId: trackingId
          } 
          appLogger.error('Close-Tote: Error fetching container details from inventory', logInfo)
          checkPrintersAndCloseTote(value);
        })
      }
      else{
        checkPrintersAndCloseTote(value);
      }
      
    },
    [checkPrintersAndCloseTote]
  );

  useScannedEventListener(
    (event) => {
      event.stopPropagation();
      // only trigger barcode scan when in scan input view
      if (action === CLOSETOTEACTIONS.SCAN_INPUT) {
        const parsed = event.detail.parsed.get("1D");
        if (parsed) {
          onBarcodeScanned(parsed);
        } else {
          setAlerts({
            success: null,
            loading: false,
            error: `Unexpected barcode scanned ${event.detail.scanned}`,
          });
        }
      }
    },
    { capture: true }
  );

  const onBarcodeScanned = useCallback(
    (value: string) => {
      onClickConfirm(value);
    },
    [onClickConfirm]
  );

  const getAction = (action: string) => {
    switch (action) {
      case CLOSETOTEACTIONS.DONE: {
        return <ToteDone onClickDone={onDoneClick} />;
      }
      case CLOSETOTEACTIONS.KEYBOARD_INPUT: {
        return (
          <InputForm
            inputLabel="Slot Id"
            onConfirm={onClickConfirm}
            title="Enter Slot Id"
            buttonDisabled={alerts.loading}
          />
        );
      }
      default: {
        return <ToteScan onClickManualEntry={onClickManualEntry} />;
      }
    }
  };

  return (
    <>
      {showHeavyLiftPopUp && printJobData && containerWeight && (
        <DialogHeavyLift 
          open={true}
          header="Heavy Lift Label"
          content={`We've identified this Box is heavy! (${containerWeight} Lbs). Please place a Heavy label and carefully handle this case.\n\n
          Esta caja es pesada! (${containerWeight} Lbs). Coloque una pagetina de 'Heavy' y manipule con cuidado esta caja.\n\n
          Cette caisse est lourde! (${containerWeight} Lbs). Ajoutez une etiquette 'Heavy' et faites attention quand vous manipulez cette caisse.`}
          handleConfirm={()=>{
            const logInfo: HeavyLiftLabel = {
              trackingId: trackingId,
              correlationId: heavyLiftCorrelationId
            };
            appLogger.info('Heavy Lift Label Popup: Confirm', logInfo)
            setShowHeavyLiftPopUp(false);
            trackingId = undefined;
            setPrintJobData(null);
            containerWeight = null;
            printInstructionPrintJob(printJobData);
          }}
        />
      )}
      <ApiStatus
        success={alerts.success}
        error={alerts.error}
        loading={alerts.loading}
      />
      <Box sx={styles.actionsWrapper}>{getAction(action)}</Box>
    </>
  );
};
